import Vue from 'vue';
import Axios from './axios';
import Api from '../conf/api';
const Http = (api, params, analysis) => {
  let data = {...Api[api]};
  if (analysis) {
    for (let key in analysis) {
      data.url = data.url.replace(new RegExp(`{${key}}`), analysis[key]);
    }
  }
  if (data.isLoading) {
    window.M.showLoading();
  }
  return Axios({
    url: data.url,
    method: data.method,
    params: data.method==='get'?params:{},
    data:data.method==='get'?{}:params,
    headers:data.headers||{},
  }).finally(() => {
    if (data.isLoading) {
      window.M.hideLoading();
    }
  })
};
export const getUrl = (url, method, params, loading) => {
  if (loading) {
    window.M.showLoading();
  }
  return Axios({
    url: url+'?'+Math.random(),
    method: method,
    params: params
  }).finally(() => {
    if (loading) {
      window.M.hideLoading();
    }
  })
};
window.Http = Vue.prototype.$http = Http;
window.GetUrl = Vue.prototype.$getUrl = getUrl;
export default Http;
