<template>
    <div id="login" class="content_top">
        <nav-bar title="忘记密码" type="red"/>
        <div class="logo">
            <img src="" alt="">
        </div>
        <div class="form">
            <form-input icon="user" type="text" v-model="params.phone" placeholder="输入手机号" @input="this.input"/>
            <form-input icon="code" type="text" v-model="params.verification_code" placeholder="短信验证码"
                        @input="this.input"
                        code="true"
                        :smsText="time===0?'发送':time+'s'"
                        :callback="()=>{this.sms()}"
            />
            <form-input icon="pwd" type="password" v-model="params.password" placeholder="输入新密码" @input="this.input"/>
        </div>
        <div class="loginBtn" :class="flag&&'active'" @click="submit">
            确定
        </div>
    </div>
</template>

<script>
  export default {
    name: "getPwd",
    data() {
      return {
        flag: false,
        time: 0,
        params: {
          phone: '',
          password: '',
          verification_key: '',
          verification_code: '',
        }
      }
    },
    beforeDestroy() {
      this.timeOut && clearInterval(this.timeOut);
    },
    mounted() {

    },
    methods: {
      input() {
        if (this.params.phone && this.params.verification_code && this.params.verification_key && this.params.password) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      },
      sms() {
        if (this.time > 0) {
          return this.$toast('请稍后发送');
        }
        if (!this.params.phone) {
          return this.$toast('手机号不能为空');
        }
        if(!(/^1[3456789]\d{9}$/.test(this.params.phone))){
          return this.$alert("手机号码有误，请重填");
        }
        this.$http('sms', {phone: this.params.phone,type:'forget_password'}).then(res => {
          this.params.verification_key = res.data.key;
          this.time = 60;
          this.timeOut = setInterval(() => {
            this.time = this.time - 1;
            if (this.time === 0) {
              this.time = 0;
              clearInterval(this.timeOut);
            }
          }, 1000)
        });
      },
      submit() {
        if (!this.flag) {
          return false;
        }
        if (!this.params.phone) {
          return this.$toast('手机号不能为空');
        }
        if (!this.params.verification_code) {
          return this.$toast('验证码不能为空');
        }
        if (!this.params.password) {
          return this.$toast('请输入密码');
        }
        this.$http('getPwd', this.params).then(() => {
          this.$alert('找回成功', () => {
            this.$navigation.push(this, {name: 'login'});
          })
        });
      }
    },
  }
</script>

<style scoped lang="less">
    #login {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        .logo {
            width: 152px;
            height: 152px;
            background: #C4191C;
            border-radius: 12px;
            margin: 80px auto 0;
        }

        .form {
            padding: 40px 39px 0 77px;
        }

        .loginBtn {
            width: 604px;
            height: 91px;
            background: #DA2723;
            opacity: 0.5;
            border-radius: 6px;
            color: rgba(255, 255, 255, .5);
            text-align: center;
            line-height: 91px;
            font-weight: 500;
            margin: 54px auto 0;
            font-size: 33px;

            &.active {
                opacity: 1;
                color: #fff;
            }
        }
    }

</style>
