<template>
    <div class="content_top news">
        <nav-bar title="消息"></nav-bar>

        <div v-show="list.length" class="body">
            <div class="links">
                <div class="item" v-for="(item,index) in list" :key="index">
                    <div class="time">
                        {{item.created_at}}
                    </div>
                    <div class="box newsListItem">
                        <div class="title">
                            <h1>{{item.title}}</h1>
<!--                            <div class="right">-->
<!--                                查看订单-->
<!--                                <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">-->
<!--                            </div>-->
                        </div>
                        <div class="info" v-html="item.content">
                        </div>
                    </div>
                </div>
                <load-more url="news" @success="success"></load-more>
            </div>
        </div>
        <div v-show="!list.length" class="noData">
            <img src="../../../assets/img/home/pic_default_news.png" alt="">
            <p>暂无消息</p>
        </div>
    </div>
</template>

<script>
  import LoadMore from "../../../components/public/loadMore";
  export default {
    name: "news",
    components: {LoadMore},
    data() {
      return {
        list: [],
      }
    },
    mounted(){
      this.$http('notifications',{});
    },
    methods:{
      success(res){
        this.list=this.list.concat(res);
        const _this=this;
        this.$nextTick(()=>{
          $('.newsListItem a').unbind('click').bind('click',function () {
            const type=$(this).data('type');
            const order_id=$(this).data('order-id');
            if(type==='10'){
              _this.$navigation.push(_this,{
                name:'orderDetails',
                query:{
                  order_id:order_id
                }
              })
            }else if(type==='20'){
              _this.$navigation.push(_this,{
                name:'details',
                query:{
                  orderDetails:order_id
                }
              })
            }
          })
        })
      }
    }
  }
</script>


