<template>
    <div class="content_top siteIndex">
        <nav-bar title="我的地址" :right="flag?'完成':'管理'" :right-callback="()=>{this.flag=!this.flag}"></nav-bar>
        <div class="body">
            <div class="list" v-show="list.length">
                <div class="item" v-for="(item,index) in list" :key="index">
                    <i v-show="flag" class="edit" :class="item.flag&&'active'" @click="item.flag=!item.flag"></i>
                    <div>
                        <div class="title">
                            <h1>{{item.contact_name}}</h1>
                            <h2>{{item.contact_phone.substr(0,4)+'****'+item.contact_phone.substr(8,11)}}</h2>
                            <h3 v-show="item.default">默认地址</h3>
                            <router-link :to="{name:'siteEdit',query: {flag:true},params:item}">
                                <img src="../../../assets/img/user/ic_personal_center_top_editor_white.png" alt="">
                            </router-link>
                        </div>
                        <div class="site">
                            {{item.province}}{{item.city}}{{item.district}}{{item.address}}
                        </div>
                    </div>
                </div>
            </div>
            <div class="noData" v-show="!list.length">
                <div class="img">
                    <img src="../../../assets/img/user/pic_default_address.png" alt="">
                </div>
                <p>请先添加地址</p>
            </div>
            <div class="btn" @click="go" v-if="!flag">
                <div>
                    +&nbsp;添加新地址
                </div>
            </div>
            <div class="btn" @click="del" v-else>
                <div>
                    删除
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "siteIndex",
    data() {
      return {
        flag: false,
        list: [],
      }
    },
    computed: {
      ...mapState({
        site: state => state.site.list.map(item => {
          return {
            ...item,
            ...{
              flag: false,
            }
          }
        })
      })
    },
    async beforeMount() {
      await this.getSiteList();
      this.list = this.site;
    },
    methods: {
      ...mapActions([
        'getSiteList'
      ]),
      go() {
        this.$navigation.push(this, {name: 'siteEdit', query: {flag: false}});
      },
      del() {
        if (!this.list.length) {
          return this.$toast('没有要删除的地址')
        }
        const ids = this.list.filter(item => {
          return item.flag;
        }).map(item => {
          return item.address_id
        });
        if (!ids.length) {
          return this.$toast('请选择要删除的地址')
        }
        this.$http('deleteSites', {address_id: ids.join(',')}).then(() => {
          this.list = this.list.filter(item => {
            return !item.flag
          })
        })
      }
    }
  }
</script>

