<template>
    <div class="content_top enterServe">
        <nav-bar title="服务商申请入驻" right="入驻须知" :right-callback="go"/>
        <div v-if="flag" class="noData">
            <img src="../../../assets/img/user/enter/pic_default_mall_entry.png" alt="">
            <p>入驻成功</p>
        </div>
        <div v-else class="body">
            <div class="uploadHeadImg">
                <h1>
                    上传店铺照片<span>(上限3张）</span>
                </h1>
                <div class="box">
                    <div class="img" v-for="(item,index) in paramsData.logos" :key="index"
                         @click="()=>{deleteLogo(index)}">
                        <img :src="item" alt="">
                    </div>
                    <div class="upload" v-show="paramsData.logos.length<3">
                        <img src="../../../assets/img/user/enter/ic_national_emblem.png" alt="">
                        <p>添加照片</p>
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="logo"
                               @change="uploadImg($event)"/>
                    </div>
                </div>
            </div>
            <div class="inputBox">
                <div class="input">
                    <div class="title">店铺名称</div>
                    <div class="text">
                        <input type="text" v-model="paramsData.params.name" placeholder="请输入店铺名称"/>
                    </div>
                </div>
                <div class="input">
                    <div class="title">联系人</div>
                    <div class="text">
                        <input type="text" v-model="paramsData.params.contact" placeholder="请输入联系人姓名"/>
                    </div>
                </div>
                <div class="input">
                    <div class="title">联系电话</div>
                    <div class="text">
                        <input type="text" v-model="paramsData.params.telphone" placeholder="请输入联系人电话"/>
                    </div>
                </div>
            </div>
            <div class="uploadCards">
                <h1>身份认证</h1>
                <div class="box">
                    <div class="uploadCard">
                        <img :src="paramsData.id_front||require('../../../assets/img/user/enter/ic_portrait_plane.png')"
                             alt="">
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="id_front"
                               @change="uploadImg($event)"/>
                    </div>
                    <div class="uploadCard">
                        <img :src="paramsData.id_back||require('../../../assets/img/user/enter/ic_national_emblem.png')"
                             alt="">
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="id_back"
                               @change="uploadImg($event)"/>
                    </div>
                </div>
                <div class="foot">
                    <div>点击上传<span>人像面</span></div>
                    <div>点击上传<span>国徽面</span></div>
                </div>
            </div>
            <div class="inputBox">
                <div class="input">
                    <div class="title">公司名称</div>
                    <div class="text">
                        <input type="text" v-model="paramsData.params.company_name" placeholder="请输入公司名称"/>
                    </div>
                </div>
                <div class="input">
                    <div class="title">公司地址</div>
                    <div class="text">
                        <input type="text" :value="paramsData.params.address" placeholder="请选择公司地址" readonly="readonly"/>
                    </div>
                    <div class="icon" @click="map">
                        <img src="../../../assets/img/user/enter/ic_address_small_gray.png" alt="">
                    </div>
                </div>
                <div class="input">
                    <div class="title">业务范围</div>
                    <div class="text">
                        <input type="text" v-model="paramsData.params.business_scope" placeholder="请输入公司服务范围"/>
                    </div>
                </div>
                <div class="input">
                    <div class="title">员工人数</div>
                    <div class="text">
                        <input type="number" v-model="paramsData.params.person_num" placeholder="员工人数"/>
                    </div>
                </div>
            </div>
            <div class="uploadHeadImg">
                <h1>
                    上传营业执照
                </h1>
                <div class="box">
                    <div class="upload">
                        <img v-if="paramsData.business_license" :src="paramsData.business_license" class="pic" alt="">
                        <img v-if="!paramsData.business_license"
                             src="../../../assets/img/user/enter/ic_national_emblem.png"
                             alt="">
                        <p v-if="!paramsData.business_license">添加照片</p>
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="business_license"
                               @change="uploadImg($event)"/>
                    </div>
                </div>
            </div>
            <div class="btn" @click="submit" v-show="this.status!=='pending'">
                <div class="active">确认入驻</div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "enterServe",
    data() {
      return {
        status: '',
        flag: false,
        paramsData: {
          logos: [],
          id_front: '',
          id_back: '',
          business_license: '',
          params: {
            name: '',//店铺名称
            logo: [],//店铺图片
            contact: '',//联系人
            telphone: '',//联系电话
            id_front: '',//身份证人头像
            id_back: '',//身份证国徽面
            company_name: '',//公司名称
            address: '',//公司地址
            business_scope: '',//业务范围
            business_license: '',//上传营业执照
            lat: 0,
            lng: 0,
            shop_type: 20,//10商家 20服务商
            person_num: 1,//员工人数
          }
        }
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo,
        address: state => state.config.map.address.find(item => {
          return item.flag === true;
        }) || {},
        global: state => state.global
      })
    },
    async beforeMount() {
      //pending审核中；failed审核失败；success审核成功
      const res = await this.$http('checkInfo');
      const {store_shop: data} = res;
      if (data.shop_type === 20) {
        this.status = data.status;
         if (data.name) {
          this.paramsData = {
            logos: data.images.map(item => {
              return item.visit_url + item.url;
            }),
            id_front: data.id_front,
            id_back: data.id_back,
            business_license: data.business_license,
            params: {
              name: data.name,//店铺名称
              logo: data.images.map(item => {
                return item.url
              }),//店铺图片
              contact: data.contact,//联系人
              telphone: data.telphone,//联系电话
              id_front: data.id_front,//身份证人头像
              id_back: data.id_back,//身份证国徽面
              company_name: data.company_name,//公司名称
              address: data.address,//公司地址
              business_scope: data.business_scope,//业务范围
              business_license: data.business_license,//上传营业执照
              shop_type: 20,//10商家 20服务商
              person_num: data.person_num,//员工人数
              lat: data.lat,
              lng: data.lng,
            }
          };
        }
        if (data.status === 'pending') {
          return this.$alert('审核中');
        } else if (data.status === 'failed') {
          return this.$prompt('审核不通过', data.remark, () => {
            this.$http('shopRest',{shop_type:20});
          }, '重新申请');
        } else if (data.status === 'success') {
          return this.flag = true;
        }
      } else {
        if (JSON.stringify(this.global.serve) !== '{}') {
          this.paramsData = this.global.serve;
        }
      }
    },
    methods: {
      ...mapActions(['setMap', 'setUserInfo', 'getGlobal']),
      go() {
        this.$navigation.push(this, {name: 'enterNotice'})
      },
      deleteLogo(index) {
        let arr1 = this.paramsData.logos;
        arr1.splice(index, 1);
        this.paramsData.logos = arr1;
        let arr2 = this.paramsData.params.logo;
        arr2.splice(index, 1);
        this.paramsData.params.logo = arr2;
      },
      uploadImg($event) {
        let formData = new FormData();
        const self = $event.target;
        const type = self.dataset.type;
        formData.append('file', self.files[0]);
        if (type === 'logo') {
          if (this.paramsData.logos.length >= 3) {
            this.$toast('最多只能上传三张');
            return false;
          }
        }
        this.$http('upload', formData).then(res => {
          if (type === 'logo') {
            this.paramsData.logos.push(res.data.visit_url);
            this.paramsData.params.logo.push(res.data.url);
          } else {
            this.paramsData[type] = res.data.visit_url;
            this.paramsData.params[type] = res.data.url;
          }
        });
      },
      submit() {
        if (this.paramsData.logos.length === 0) {
          return this.$toast('最少上传一张店铺图片')
        }
        if (!this.paramsData.params.name) {
          return this.$toast('请输入店铺名字')
        }
        if (this.paramsData.params.name.length > 15) {
          return this.$toast('店铺名字最长只能15位')
        }
        if (this.paramsData.params.name.match(/^[0-9a-zA-Z\u4e00-\u9fa5]+$/) === null) {
          return this.$toast('店铺名字只能是数字字母或者汉字')
        }
        if (!this.paramsData.params.contact) {
          return this.$toast('请输入联系人')
        }
        if (!this.paramsData.params.telphone) {
          return this.$toast('请输入联系人电话')
        }
        if (!(/^1[3456789]\d{9}$/.test(this.paramsData.params.telphone))) {
          return this.$alert("联系人手机号码不正确");
        }
        if (!this.paramsData.params.id_front) {
          return this.$toast('请上传身份证正面')
        }
        if (!this.paramsData.params.id_back) {
          return this.$toast('请上传身份证反面')
        }
        if (!this.paramsData.params.company_name) {
          return this.$toast('请输入公司名称')
        }
        if (!this.paramsData.params.lat || !this.paramsData.params.lng) {
          return this.$toast('请选择店铺位置')
        }
        if (!this.paramsData.params.business_scope) {
          return this.$toast('请输入业务范围')
        }
        if (!this.paramsData.params.business_license) {
          return this.$toast('请上传营业执照')
        }

        if (this.paramsData.params.person_num < 1) {
          return this.$toast('员工人数不能小于1')
        }
        this.paramsData.params.logo = this.paramsData.params.logo.join(',');
        this.$http('addShop', this.paramsData.params).then(() => {
          this.$toast('提交成功');
          this.$navigation.back(this);
        })
      },
      map() {
        this.getGlobal({
          key: 'serve',
          data: this.paramsData
        });
        this.$navigation.push(this, {
          name: 'siteMap',
          query: {name: 'serve'}
        });
      },
    }
  }
</script>

