import ModalComponent from '../components/public/toast'// 引入先前写好的vue

const Modal = {};

// 注册Toast
Modal.install = function (Vue) {
  // 生成一个Vue的子类
  const ModalConstructor = Vue.extend(ModalComponent);
  // 生成一个该子类的实例
  const modal = new ModalConstructor();

  // 将这个实例挂载在我创建的div上
  // 并将此div加入全局挂载点内部
  modal.$mount(document.createElement('div'));
  document.body.appendChild(modal.$el);

  const toast = (title, callback) => {
    modal.type = 'toast';
    modal.title = title;
    modal.callbackOk = callback;
    modal.flag = true;
  };
  const hide = () => {
    modal.flag = false;
  };

  window.M.toast = Vue.prototype.$toast = toast;
  window.M.hideToast = Vue.prototype.$hideToast = hide;
};
export default Modal;
