const router = new VueRouter({
  mode: 'history',
  // base: '/web',
  routes // (缩写) 相当于 routes: routes
});
router.beforeEach((to, form, next) => {
  let token = false;
  try {
    token = JSON.parse(localStorage.getItem('vuex')).user.token.access_token;
  } catch (e) {
    token = false;
  }
  if (to.meta.auth === true && !token) {
    next({name: 'login', params: {animate: 1}});
  } else {
    next();
  }
});
export default router;
