<template>
    <div class="searchBar">
        <status-bar/>
        <table></table>
        <div class="search">
            <div class="body">
                <router-link :to="{name:'search'}" class="left">
                    <div class="icon">
                        <img src="../../assets/img/home/ic_search_gray.png" alt>
                    </div>
                    <div class="title">请输入你想搜索的商品~</div>
                </router-link>
                <router-link :to="{name:'news'}" class="right active">
                    <img src="../../assets/img/home/ic_news_white.png" alt>
                    <div class="info" v-show="newsNum"></div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState} from 'vuex';

  export default {
    name: "searchBar",
    data() {
      return {
        newsNum: 0,
      }
    },
    computed: {
      ...mapState({
        token: state => state.user.token.access_token
      })
    },
    mounted() {
      if (this.token) {
        this.getNewsNum();
      }
    },
    methods: {
      getNewsNum() {
        this.$http('newsNum').then(res => {
          this.newsNum = res.data.unread_count;
        })
      }
    }
  };
</script>

