<template>
    <transition name="slide">
        <div class="sku" v-show="flag" @click="flag=false">
            <div class="body" @click.stop>
                <div class="list">
                    <div class="item" v-for="(item,index) in data" :key="index">
                        <h1>{{item.name}}</h1>
                        <div class="children">
                            <span v-for="(x,y) in item.items" :key="y" :class="x.flag&&'active'"
                                  @click="()=>{select(index,y)}">{{x.value}}</span>
                        </div>
                    </div>
                </div>
                <div class="numBox">
                    <div class="left">
                        数量
                    </div>
                    <div class="right">
                        <span @click="num<=1?1:num--" :class="num>1&&'active'">-</span>
                        <div>{{num}}</div>
                        <span class="active" @click="num++">+</span>
                    </div>
                </div>
                <div class="btn" @click="submit">
                    确定
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
  export default {
    name: "sku",
    data() {
      return {
        flag: false,
        data: [],
        num: 1,
      }
    },
    methods: {
      select(x, y) {
        this.data[x].items = this.data[x].items.map((item, index) => {
          if (index === y) {
            return {
              ...item,
              ...{
                flag: true,
              }
            }
          }
          return {
            ...item,
            ...{
              flag: false,
            }
          }
        });
        this.num = 1;
      },
      submit() {
        let data={
          num:this.num,
          data:this.data
        };
        this.flag=false;
        this.callback&&this.callback(data);
      }
    }
  }
</script>

<style scoped lang="less">
    .slide-enter-active {
        animation-name: slideInUp;
        animation-duration: 0.2s;
        animation-fill-mode: both;
    }

    .slide-leave-active {
        animation-name: slideOutDown;
        animation-duration: 0.2s;
        animation-fill-mode: both;
    }

    @keyframes slideInUp {
        0% {
            transform: translate3d(0, 100%, 0);
            visibility: visible;
        }

        to {
            transform: translateZ(0);
        }
    }

    @keyframes slideOutDown {
        0% {
            transform: translateZ(0);
        }

        to {
            visibility: hidden;
            transform: translate3d(0, 100%, 0);
        }
    }

    .delay-leave-active {
        -webkit-animation-delay: 0.2s;
        -moz-animation-delay: 0.2s;
        -o-animation-delay: 0.2s;
        animation-delay: 0.2s;
    }

