<template>
    <div class="content_top orderEvaluate">
        <nav-bar title="发表评价" right="提交" :rightCallback="submit"></nav-bar>
        <div class="body">
            <div class="box">
                <div class="top">
                    <div v-for="(item,index) in params.comments" :key="index">
                        <div>
                            <img :src="item.logo" alt="">
                        </div>
                        <span>商品评分</span>
                        <img v-for="n in total" v-if="n<=item.grade"
                             src="../../../assets/img/home/ic_stars_small_yellow.png"
                             @click="item.grade=n"/>
                        <img v-for="n in total" v-if="n>item.grade"
                             src="../../../assets/img/home/ic_stars_small_gray.png" @click="item.grade=n"/>
                    </div>
                </div>
                <div class="center">
                    <textarea placeholder="请对该商品进行评价" v-model="content" @input="input"></textarea>
                </div>
                <div class="bottom">
                    <ul>
                        <li v-for="(item,index) in images" :key="index" @click="()=>del(index)">
                            <img :src="item" alt="">
                        </li>
                        <li v-show="images.length<6">
                            <img src="../../../assets/img/shop/ic_add_pictures.png" alt="">
                            <input type="file" @change="uploadImg($event)">
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "shopEvaluate",
    data() {
      return {
        images: [],
        paramsImages: [],
        content: '',
        total: 5,
        params: {
          order_id: 0,
          comments: [
            // {
            //   sku_id: 1,
            //   product_id: 0,
            //   grade: 5,
            //   logo:'',
            //   order_item_id: 0,
            //   images: [],
            //   content: '',
            // }
          ]
        }
      }
    },
    beforeMount() {
      if (!this.$route.params.data) {
        this.$alert('信息有误', () => {
          this.$navigation.back(this)
        });
      } else {
        this.params = this.$route.params.data;
      }
    },
    methods: {
      del(x) {
        const arr = this.images;
        const arr2=this.paramsImages;
        arr.splice(x,1);
        arr2.splice(x,1);
        this.images = arr;
        this.paramsImages=arr2;
      },
      input(){
        if(this.content.length>=100){
          this.content=this.content.substr(0,100)
        }
      },
      uploadImg($event) {
        let formData = new FormData();
        const self = $event.target;
        formData.append('file', self.files[0]);
        this.$http('upload', formData).then(res => {
          this.images.push(res.data.visit_url);
          this.paramsImages.push(res.data.url);
        });
      },
      submit() {
        this.params.comments = this.params.comments.map(item => {
          return {
            ...item,
            ...{
              images: this.paramsImages.join(','),
              content: this.content,
            }
          }
        });
        this.$http('orderEvaluate', this.params).then(() => {
          this.$navigation.back(this);
        })
      }
    }
  }
</script>

