<template>
    <div class="content_top invoiceIssue">
        <nav-bar title="发票常见问题"></nav-bar>
        <div class="body">
            <p>1. 发票的类型有哪些？</p>
            <p>目前可以开具电子增值税普通发票(简称“电子发票”)、纸质增值税普通发票(简称“纸质发票”)、以及增值税专用发票(简称“专用发票”)。</p>
            <p>2. 发票内容可以开什么？</p>
            <p>订单中含有办公用品（箱包、文具、口罩），可选开票内容：办公用品等、耗材等、日用品、家居用品、食品、酒/饮料、服饰、化妆品；
                不含有办公用品，可选开票内容：日用品、家居用品、食品、酒/饮料、服饰、化妆品；礼品卡开票内容：预付卡。</p>
            <p>3. 发票的金额包含运费或活动金额吗？</p>
            <p>发票总金额等于您实际支付的消费金额（含运费），不包括未实际支出的折扣的金额和使用礼品卡支付的金额。</p>
            <p>4. 为什么我的订单不支持开发票？</p>
            <p>当您的订单金额为0元或者该订单完全使用礼品卡支付的时候；或特殊渠道下单时不支持开发票。</p>
            <p>5. 为什么使用礼品卡支付时不支持开发票？</p>
            <p>在购买礼品卡时已支持开具发票，因此在使用礼品卡消费时，不再支持重复开票。</p>
