import Axios from 'axios';
import conf from '../conf/conf'

Axios.defaults.baseURL = conf.baseUrl;
// 添加请求拦截器
Axios.interceptors.request.use(function (config) {
  try {
    config.headers.Authorization = 'Bearer ' + JSON.parse(localStorage.getItem('vuex')).user.token.access_token;
  } catch (e) {
    config.headers.Authorization = '';
  }
  return config;
}, function (error) {
  window.M.alert('系统出现异常');
  throw new Error(error);
});

// 添加响应拦截器
Axios.interceptors.response.use(function (response) {
  return response.data;
}, function (error) {
  if (error.response.data.message) {
    if (error.response.data.status_code === 401) {
      return window.M.alert('身份认证失败,请从新登陆', () => {
        window.location.href = '/login';
      })
    } else if (error.response.data.status_code === 403) {
      return window.M.prompt(error.response.data.message,error.response.data.remark,() => {
        window.location.href='/login';
      },'知道了');
    }else if(error.response.data.message==='该商品库存不足'){
      window.M.toast(error.response.data.message,()=>{
        window.history.go(-1)
      });
    }else{
      window.M.toast(error.response.data.message);
    }
  }
  throw new Error(error);
});
export default Axios;
