<template>
    <div class="particulars">
        <div class="body">
            <div class="shopImg">
                <carousel :data="info.images" type="3"></carousel>
                <div class="back" @click="$router.back()"></div>
            </div>
            <div class="head">
                <h1>{{info.name}}</h1>
                <div class="site">
                    <img src="../../../assets/img/shop/ic_shop_address.png" alt="">
                    <div class="info">
                        {{info.address}}
                    </div>
                </div>
                <div class="starBox">
                    <div class="star">
                        <star class="star" :num="parseFloat(info.star)"></star>
                    </div>
                    <div class="info">{{info.distance}}米</div>
                </div>
            </div>
            <div class="serve" @click="showTypeList">
                <div class="left">
                    选择服务类型
                </div>
                <div class="right">
                    <span>{{params.service.name}}</span>
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </div>
            </div>
            <div class="notice">
                <notice :data="info.notice" :shop_id="info.shop_id"></notice>
            </div>
            <div class="comment">
                <router-link :to="{name:'evaluate',query:{url:'serviceEvaluateList',shop_id:$route.query.shop_id}}"
                             class="comment">
                    评论({{info.comments.total}}）
                    <span>查看更多</span>
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </router-link>
                <div class="box">
                    <remark v-for="(item,index) in info.comments.data" :key="index" :data="item"></remark>
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="left" :class="info.favored&&'active'" @click="favorites">
                <img v-if="!info.favored" src="../../../assets/img/shop/ic_collection_no_choose_black.png" alt="">
                <img v-else src="../../../assets/img/shop/ic_collection_selected_red.png" alt="">
                <span>收藏</span>
            </div>
            <div class="right"
                 @click="submit">
                立即预约
            </div>
        </div>
    </div>
</template>

<script>
  import Notice from "../../../components/home/notice";
  import Remark from "../../../components/shop/remark";
  import Star from "../../../components/public/star";
  import Carousel from "../../../components/home/carousel";
  import {mapActions, mapState} from 'vuex';

  export default {
    name: "particulars",
    computed: {
      ...mapState({
        map: state => state.config.map,
        global: state => state.global
      })
    },
    data() {
      return {
        typeList: [
          {
            subs: [
              {
                subs: []
              }
            ]
          }
        ],
        info: {
          images: [],
          notice: [],
          comments: {},
        },
        params: {
          service: {
            name: ''
          }
        }
      }
    },
    components: {Carousel, Star, Remark, Notice},
    async mounted() {
      this.getGlobal({
        key: 'putMake',
        data: {}
      });
      this.getInfo();
      await this.getTypeList();
    },
    methods: {
      ...mapActions([
        'getGlobal'
      ]),
      async getTypeList() {
        const res = await this.$http('serviceTypeLis', {}, {shop_id: this.$route.query.shop_id});

        this.typeList = res.data.map(item => {
          return {
            ...item,
            ...{
              subs: !item.sub ? [] : item.sub.map(x => {
                return {
                  ...x,
                  ...{
                    subs: !x.sub ? [] : x.sub.map(y => {
                      return {
                        ...y,
                        ...{
                          subs: y.sub || []
                        }
                      }
                    })
                  }
                }
              })
            }
          }
        });
        let data;
        if (this.typeList.length) {
          if (this.typeList[0]) {
            data = this.typeList[0];
            if (data.subs[0]) {
              data = data.subs[0];
              if (data.subs[0]) {
                data = data.subs[0];
              }
            }
          }
          this.params.service = data;
        }
      },
      getInfo() {
        let point = {};
        if (this.$route.query.type === 20) {
          point = {
            ...this.$route.query.point
          }
        } else {
          point = {
            lat:this.map.lat||0,
            lng:this.map.lng||0
          }
        }
        this.$http('shopDetails', {...point}, {shop_id: this.$route.query.shop_id}).then(res => {
          this.info = res.data;
        })
      },
      showTypeList() {
        this.$serviceShow(this.typeList, (data) => {
          this.params.service = data;
        })
      },
      favorites() {
        this.$http(this.info.favored ? 'cancelShopFavorites' : 'shopFavorites', {target_id: this.info.shop_id}).then(() => {
          this.info.favored = !this.info.favored;
        })
      },
      submit() {
        if (!this.typeList.length) {
          return this.$alert('该店铺暂时无法预约');
        }
        this.$navigation.push(this, {
          name: 'putMake',
          query: {
            shop_id: this.$route.query.shop_id,
            cate_id: this.params.service.cate_id,
            type: this.$route.query.type
          }
        });
      }
    }
  }
</script>

