<template>
    <div class="content_top visit">
        <div class="build_search">
            <div class="left" @click="()=>{this.$navigation.back(this)}">
                <img src="../../../assets/img/icon/ic_arrow_left_return_black.png" alt="">
            </div>
            <div class="center">
                <div class="icon">
                    <img src="../../../assets/img/home/ic_search_gray.png" alt="">
                </div>
                <div class="input">
                    <input type="text" placeholder="搜索店铺名称/服务类型" v-model="paramsData.params.keyword">
                </div>
            </div>
            <div class="right" @click="submit">
                搜索
            </div>
        </div>
        <div class="retrieve">
            <div :class="paramsData.flag===1&&'active'"
                 @click.stop="paramsData.flag&&flag===1?(paramsData.flag=0):paramsData.flag=1">
                <span>{{paramsData.params.sort==='distance'?'距离筛选':'星级服务'}}</span>
                <div class="items" v-show="paramsData.flag===1">
                    <div class="one">
                        <div :class="paramsData.params.sort==='distance'&&'active'"
                             @click="paramsData.params.sort='distance'">距离排序
                        </div>
                        <div :class="paramsData.params.sort==='service'&&'active'"
                             @click="paramsData.params.sort='service'">星级排序
                        </div>
                    </div>
                </div>
            </div>
            <div :class="paramsData.flag===2&&'active'"
                 @click.stop="paramsData.flag&&paramsData.flag===2?(paramsData.flag=0):paramsData.flag=2">
                <span>{{paramsData.serviceName}}</span>
            </div>
            <div :class="paramsData.flag===3&&'active'" @click.stop="goMap">
                <span>{{paramsData.siteName}}</span>
            </div>
        </div>
        <div class="body">
            <div class="list">
                <item v-for="(item,index) in list" :key="index" :data="item" :serviceType="20"
                      :point="{lat:paramsData.params.lat,lng:paramsData.params.lng}"></item>
                <load-more ref="loadMore" url="serviceShop" :params="paramsData.params" @success="success"></load-more>
            </div>
        </div>
        <div class="items" v-show="paramsData.flag===2">
            <div class="two service">
                <div class="body">
                    <div class="info">
                        <div>
                            <ul>
                                <li :class="index===paramsData.oneIndex&&'active'" v-for="(item,index) in data"
                                    :key="index"
                                    @click.stop="()=>{selectIndex('oneIndex',index)}">{{item.name}}
                                </li>
                            </ul>
                        </div>
                        <div>
                            <ul>
                                <li :class="index===paramsData.twoIndex&&'active'"
                                    v-for="(item,index) in paramsData.twoArr"
                                    :key="index" @click.stop="()=>{selectIndex('twoIndex',index)}">{{item.name}}
                                </li>
                            </ul>
                        </div>
                        <div>
                            <ul>
                                <li :class="index===paramsData.threeIndex&&'active'"
                                    v-for="(item,index) in paramsData.threeArr"
                                    :key="index" @click.stop="()=>{selectIndex('threeIndex',index)}">{{item.name}}
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="btn">
                        <div @click="this.rest">重置</div>
                        <div @click="this.submitService">确定</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="bg" v-show="paramsData.flag"></div>
    </div>
</template>

<script>
  import Item from "../../../components/shop/item";
  import LoadMore from "../../../components/public/loadMore";
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "visit",
    components: {LoadMore, Item},
    computed: {
      ...mapState({
        map: state => state.config.map,
        serviceTypeList: state => state.service.serviceTypeList,
        global: state => state.global
      }),
    },
    watch: {},
    data() {
      return {
        data: [],
        paramsData: {
          oneIndex: 0,
          twoIndex: 0,
          threeIndex: 0,
          twoArr: [],
          threeArr: [],
          flag: 0,
          serviceName: '服务类型',
          siteName: '',
          params: {
            lng: 10,
            lat: 10,
            sort: 'distance',
            service_id: '',
            keyword: '',
          },
        },
        list: [],
      }
    },
    beforeMount() {
      if (JSON.stringify(this.global.visit) !== "{}") {
        this.paramsData = this.global.visit;
      } else {
        try {
          this.paramsData.params.lng = this.map.lng;
          this.paramsData.params.lat = this.map.lat;
          this.paramsData.siteName = this.map.site;
        } catch (e) {

        }
        this.data = this.serviceTypeList;
        this.submitService();
        this.selectIndex('oneIndex', 0);
      }
    },
    methods: {
      ...mapActions(['getGlobal']),
      selectIndex(type, index) {
        if (type === 'oneIndex') {
          this.paramsData.twoIndex = 0;
          try {
            this.paramsData.twoArr = this.data[index].subs;
          } catch (e) {
            this.paramsData.twoArr = [];
          }
        }
        if (type === 'twoIndex') {
          this.paramsData.threeIndex = 0;
          try {
            this.paramsData.threeIndex = this.data[this.paramsData.oneIndex].subs[index].subs;
          } catch (e) {
            this.paramsData.threeIndex = [];
          }
        }
        this.paramsData[type] = index;
      },
      submitService() {
        let data;
        if (this.data[this.paramsData.oneIndex]) {
          data = this.data[this.paramsData.oneIndex];
          if (data.subs[this.paramsData.twoIndex]) {
            data = data.subs[this.paramsData.twoIndex];
            if (data.subs[this.paramsData.threeIndex]) {
              data = data.subs[this.paramsData.threeIndex];
            }
          }
        }
        this.paramsData.serviceName = data.name || '';
        this.paramsData.params.service_id = data.id || '';
        this.paramsData.flag = 0;
      },
      rest() {
        this.paramsData.oneIndex = 0;
        this.paramsData.twoIndex = 0;
        this.paramsData.threeIndex = 0;
        this.paramsData.serviceName = '服务类型';
        this.paramsData.params.service_id = "";
      },
      success(res) {
        this.list = this.list.concat(res)
      },
      submit() {
        this.list = [];
        this.$refs.loadMore.restart();
      },
      selectSite() {
        this.paramsData.flag = 0;
      },
      goMap() {
        this.getGlobal({
          key: 'visit',
          data: this.paramsData
        });
        this.$navigation.push(this, {name: 'siteMap', query: {name: 'visit'}})
      }
    }
  }
</script>

