<template>
    <div class="content home">
        <search-bar/>
        <nav-foot/>
        <carousel :data="carousel" type="1"/>
        <div class="menus">
            <router-link :to="{name:'visit'}"></router-link>
            <router-link :to="{name:'haoyin'}"></router-link>
        </div>
        <notice :data="home_notice" shop_id=""></notice>
        <div class="nearbyShop">
            <div class="title">
                <div class="icon"></div>
                <div class="info">附近好店</div>
            </div>
            <div class="body">
                <item v-for="(item,index) in list " :key="index" :data="item"/>
                <load-more ref="loadMore" url="near" :autoload="false" :params="params" @success="success"></load-more>
            </div>
        </div>
    </div>
</template>

<script>
  import searchBar from '../../components/home/searchBar';
  import carousel from '../../components/home/carousel';
  import item from '../../components/shop/item';
  import Notice from "../../components/home/notice";
  import {mapState, mapActions} from 'vuex';
  import LoadMore from "../../components/public/loadMore";

  export default {
    name: "homeIndex",
    data() {
      return {
        list: [],
        params: {
          lng: 0,
          lat: 0,
        }
      }
    },
    components: {LoadMore, Notice, searchBar, carousel, item},
    computed: {
      ...mapState({
        carousel: state => state.carousel.carousel,
        home_notice: state => state.notice.home_notice,
        map: state => state.config.map,
      })
    },
    mounted() {
      this.getGlobal({
        key:'visit',
        data:{}
      });
      this.setSearch({
        sort: 'distance',
        service_id: '',
        keyword: '',
        flag:false,
      });
      if (this.map.lng && this.map.lat) {
        this.params.lng = this.map.lng;
        this.params.lat = this.map.lat;
        this.$refs.loadMore.restart();
      } else {
        this.$showLoading('正在获取定位');
        this.$locationPoint(res => {
          console.log(1111111111,res)
          this.$hideLoading();
          if (res) {
            if (res.code === 200) {
              this.params.lng = res.data.lng;
              this.params.lat = res.data.lat;
              this.setMap(res.data);
              this.$refs.loadMore.restart();
            } else {
              this.$alert('获取定位失败,请稍后再试');
            }
          } else {
            this.$alert('获取定位失败,请稍后再试');
          }
        });
      }

      this.getCarouselShop();
      this.getHomeNotice();
    },
    methods: {
      ...mapActions(['getCarouselShop', 'getHomeNotice', 'setMap','setSearch','getGlobal']),
      success(data) {
        this.list = this.list.concat(data)
      },
    }
  }
</script>

