<template>
    <div class="content_top siteMap">
        <nav-bar title="定位地址" right="确定" :right-callback="()=>{this.ok()}"></nav-bar>
        <div class="body">
            <div class="search">
                <div>
                    <img src="../../../assets/img/home/ic_search_gray.png" alt="">
                    <input type="text" v-model="keyword" @change="submit">
                    <img v-show="keyword" src="../../../assets/img/home/visit/ic_circular_cancel.png" alt=""
                         @click="keyword=''">
                </div>
            </div>
            <div class="site">
                <h1 @click="changeMap"><img src="../../../assets/img/home/visit/ic_relocation_green.png" alt="">重新定位
                </h1>
                <div class="list">
                    <ul>
                        <li :class="item.flag&&'active'" v-for="(item,index) in map.address" :key="index"
                            @click="select(index)">
                            <img src="../../../assets/img/home/visit/ic_address_small_gray.png" alt="">
                            <div>
                                <h2>{{item.title}}</h2>
                                <p>{{item.address}}</p>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapActions, mapState} from 'vuex';

  export default {
    name: "siteMap",
    data() {
      return {
        keyword: '',
      }
    },
    computed: {
      ...mapState({
        map: state => state.config.map,
        global: state => state.global
      })
    },
    beforeMount() {
      this.submit();
    },
    methods: {
      ...mapActions(['setMap', 'getGlobal']),
      submit() {
        this.$showLoading('正在获取定位');
        this.$locationAddress(this.keyword, res => {
          this.$hideLoading();
          if (res) {
            if (res.code === 200) {
              this.setMap({
                ...this.map,
                address: res.data.address.map((item, index) => {
                  return {
                    ...item,
                    ...{
                      flag: index === 0
                    }
                  }
                })
              })
            } else {
              this.$alert('获取定位失败,请稍后再试');
            }
          } else {
            this.$alert('获取定位失败,请稍后再试');
          }
        });
      },
      changeMap() {
        this.$showLoading('正在获取定位');
        this.$locationAddress('', res => {
          this.$hideLoading();
          if (res) {
            if (res.code === 200) {
              this.setMap({
                address: res.data.address.map((item, index) => {
                  return {
                    ...item,
                    ...{
                      flag: index === 0
                    }
                  }
                })
              })
            } else {
              this.$alert('获取定位失败,请稍后再试');
            }
          } else {
            this.$alert('获取定位失败,请稍后再试');
          }
        })
      },
      select(x) {
        this.setMap({
          address: this.map.address.map((item, index) => {
            return {
              ...item,
              ...{
                flag: index === x
              }
            }
          })
        })
      },
      ok() {
        const name = this.$route.query.name;
        const address = this.map.address.find((item) => {
          return item.flag === true;
        });
        if (name === 'visit') {
          this.getGlobal({
            key: name,
            data: {
              ...this.global[name],
              siteName: address.title,
              params: {
                ...this.global[name].params,
                ...address.point
              }
            }
          })
        }else{
          this.getGlobal({
            key: name,
            data: {
              ...this.global[name],
              params:{
                ...this.global[name].params,
                ...address.point,
                address:address.address
              }
            }
          })
        }
        this.$navigation.back(this);
      },
    }
  }
</script>

