import ModalComponent from '../components/public/service'// 引入先前写好的vue

const Modal = {};

// 注册Toast
Modal.install = function (Vue) {
  // 生成一个Vue的子类
  const ModalConstructor = Vue.extend(ModalComponent);
  // 生成一个该子类的实例
  const modal = new ModalConstructor();

  // 将这个实例挂载在我创建的div上
  // 并将此div加入全局挂载点内部
  modal.$mount(document.createElement('div'));
  document.body.appendChild(modal.$el);

  // 通过Vue的原型注册一个方法
  // 让所有实例共享这个方法
  const Show = (data, callback) => {
    modal.flag = true;
    modal.data = data;
    modal.callback = callback
  };
  const Hidden = () => {
    modal.flag = false;
  };
  window.M.serviceShow = Vue.prototype.$serviceShow = Show;
  window.M.serviceHidden = Vue.prototype.$serviceHidden = Hidden;
};
export default Modal;
