<template>
    <div class="content_top storeIndex">
        <div class="navBar">
            <status-bar/>
            <div class="nav">
                <div class="left" @click="()=>{this.$navigation.back(this)}">
                    <img src="../../../assets/img/icon/ic_arrow_left_return_black.png" alt="">
                </div>
                <div class="title">店铺主页</div>
                <div class="right">
                    <img src="../../../assets/img/search/ic_shop_search.png"
                         @click="()=>{this.$navigation.push(this,{name:'haoyinSearch',query:{shop_id:this.$route.query.shop_id}})}"
                         alt="">
                    <img src="../../../assets/img/search/ic_shopping_cart.png"
                         @click="()=>{this.$navigation.push(this,{name:'cart'})}" alt="">
                </div>
            </div>
        </div>
        <div class="body">
            <div class="head">
                <div class="left">
                    <img
                        :src="info.logo"
                        alt="">
                </div>
                <div class="center">
                    <h1>{{info.name}}</h1>
                    <p>{{info.introduce}}</p>
                </div>
                <div class="right">
                    <img v-if="!info.favored" src="../../../assets/img/shop/ic_collection_no_choose_black.png" @click="favorites" alt="">
                    <img v-else src="../../../assets/img/shop/ic_collection_selected_red.png" @click="favorites" alt="">
                </div>
            </div>
            <div class="tjTitle">
                <h1>商品推荐</h1>
            </div>
            <div v-show="list.length" class="lists">
                <item type="2" v-for="(item,index) in list" :key="index" :data="item"></item>
                <load-more url="shopList" :params="params" @success="success"></load-more>
            </div>
            <div v-show="!list.length" class="noData">
                <img src="../../../assets/img/shop/store/pic_default_shop.png" alt="">
                <p>暂无更多商品</p>
            </div>
        </div>
        <router-link :to="{name:'storeClassify',query: {shop_id:$route.query.shop_id}}" class="footer">
            查看全部分类
        </router-link>
    </div>
</template>

<script>
  import Item from "../../../components/shop/item";
  import LoadMore from "../../../components/public/loadMore";

  export default {
    name: "storeIndex",
    data() {
      return {
        flag: false,
        info: {},
        list: [],
        params: {
          shop_id: this.$route.query.shop_id
        }
      }
    },
    components: {LoadMore, Item},
    beforeMount() {
      this.getInfo();
    },
    methods: {
      getInfo() {
        this.$http('shopHome', {}, this.$route.query).then(res => {
          this.info = res.data;
        })
      },
      success(data) {
        this.list = this.list.concat(data)
      },
      favorites() {
        this.$http(this.info.favored ? 'cancelShopFavorites' : 'shopFavorites', {target_id: this.$route.query.shop_id}).then(() => {
          this.info.favored = !this.info.favored;
        })
      }
    },
  }
</script>

