<template>
    <div>
        <nav-bar title="我的收藏" right="编辑" :right-callback="()=>{this.flag=!this.flag}"/>
        <div class="item" v-for="(item,index) in list" :key="index">
            <div class="editBox" v-show="flag">
                <i class="edit" :class="item.flag&&'active'" @click="item.flag=!item.flag"></i>
            </div>
            <router-link :to="{name:'particulars',query:{shop_id:item.shop_id}}" class="left">
                <img :src="item.shop.logo" alt="">
            </router-link>
            <router-link :to="{name:'particulars',query:{shop_id:item.shop_id}}" class="right">
                <h1 class="title">{{item.shop.name}}</h1>
                <p>{{item.shop.introduce}}&nbsp;</p>
                <div class="bottom">
                    <div class="start">
                        <star :num="item.shop.star.service"></star>
                    </div>
                    <div class="info">{{item.shop.distance}}米</div>
                </div>
            </router-link>
        </div>
        <load-more url="favorites" :params="params" @success="success"></load-more>
        <div class="deleteFooter" v-show="flag">
            <div class="left">
                <i class="edit" :class="allFLag&&'active'" @click="selectAll"></i>
                <span @click="selectAll">全选</span>
            </div>
            <div class="right" @click="del">
                删除
            </div>
        </div>
    </div>
</template>

<script>
  import LoadMore from "../../../components/public/loadMore";
  import Star from "../../../components/public/star";
  import {mapState} from 'vuex';
  export default {
    name: "collectSubscribe",
    components: {Star, LoadMore},
    data() {
      return {
        flag: false,
        list: [],
        params: {
          shop_type: 20,
        }
      }
    },
    computed: {
      ...mapState({
        map:state=>state.config.map
      }),
      allFLag() {
        let flag = true;
        this.list.map(item => {
          if (item.flag === false) {
            flag = false;
          }
        });
        return flag;
      }
    },
    beforeMount(){
      this.params={
        ...this.params,
        lng:this.map.lng,
        lat:this.map.lat,
      }
    },
    methods: {
      selectAll() {
        this.list = this.list.map(item => {
          return {
            ...item,
            flag: !this.allFLag
          }
        })
      },
      del() {
        const target_id = this.list.filter(item => {
          return item.flag;
        }).map(item => {
          return item.shop_id
        }).join(',');
        if (!target_id) {
          return this.$toast('请选择店铺');
        }
        this.$http('delFavorites', {
          target_id: target_id
        }).then(() => {
          this.list = [];
          this.$refs.loadMore.restart();
        })
      },
      success(data) {
        const newData = data.map(item => {
          return {
            ...item,
            flag: false
          }
        });
        this.list = this.list.concat(newData);
      }
    }
  };
</script>

