const Navigation = {};
Navigation.install = (Vue) => {
  Vue.prototype.$navigation = {
    push(_this, params) {
      const data = {...params, ...{params: {...params.params, ...{animate: 1}}}};
      _this.$router.push(data);
    },
    back(_this) {
      _this.$router.back();
    },
    replace(_this, params, type) {
      const data = {...params, ...{params: {...params.params, ...{animate: type ? 1 : 0}}}};
      _this.$router.replace(data);
    },
  };
};
export default Navigation;

