<template>
    <div class="body">
        <service-order-item v-for="(item,index) in list" :key="index" :data="item" @restart="restart"></service-order-item>
        <load-more ref="loadMore" url="serviceOrder" :params="params" @success="success"></load-more>
    </div>
</template>

<script>
  import ServiceOrderItem from "../../../components/user/serviceOrderItem";
  import LoadMore from "../../../components/public/loadMore";

  export default {
    name: "serviceOrderGoods",
    data() {
      return {
        list: [],
        params: {
          state: 30,
        }
      }
    },
    components: {LoadMore, ServiceOrderItem},
    methods: {
      success(data) {
        this.list = this.list.concat(data);
      },
      restart(){
        this.list=[];
        this.$refs.loadMore.restart();
      }
    }
  }
</script>

<style scoped lang="less">
    .body {
        padding: 80px 0 18px 0;
    }
</style>

