let QueryString = {}
QueryString.install = (Vue) => {
    function initial() {
        config()
    }
    function config() {
        Vue.mixin({
            data() {
                return {

                }
            },
            filters: {

            },
            created() {
              window.postWebMessage = this.postWebMessage
            },
            methods: {
              postWebMessage(json) {
                this.receiveAppMessage(this, json)
              }
            }
        })
    }
    initial()
}

export default QueryString
