<template>
    <div class="loadmore">
        <div class="loadmore__header"></div>
        <div class="loadmore__body">
            <slot></slot>
        </div>
        <div class="loadmore__footer">
           <span v-if="loading">
            <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" class="lds-coffee"><defs><linearGradient id="gradient" x1="0%" x2="0%" y1="0%" y2="100%"><stop offset="10%" stop-color="black" stop-opacity="0"></stop><stop offset="100%" stop-color="white" stop-opacity="1"></stop></linearGradient><mask id="mask" maskUnits="userSpaceOnUse" x="0" y="0" width="100" height="100"><rect x="22" y="8" width="56" height="54" fill="url(#gradient)"></rect></mask><path id="steam" d="M0-4c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9l0,0c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9l0,0 c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9l0,0c-2.1,2.6-2.1,6.4,0,9l0,0 c2.1,2.6,2.1,6.4,0,9l0,0c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9c-2.1,2.6-2.1,6.4,0,9l0,0c2.1,2.6,2.1,6.4,0,9l0,0 c-2.1,2.6-2.1,6.4,0,9h0c2.1,2.6,2.1,6.4,0,9h0c-2.1,2.6-2.1,6.4,0,9h0c2.1,2.6,2.1,6.4,0,9" stroke-width="6" stroke-linecap="round" fill="#f00" stroke="#da3732"></path></defs><g mask="url(#mask)"><use x="29" y="-0.178952" href="#steam"><animate attributeName="y" calcMode="linear" values="4;-14" keyTimes="0;1" dur="0.5" begin="0s" repeatCount="indefinite"></animate></use><use x="47" y="-8.3579" href="#steam"><animate attributeName="y" calcMode="linear" values="0;-18" keyTimes="0;1" dur="0.25" begin="0s" repeatCount="indefinite"></animate></use><use x="64" y="-19.2684" href="#steam"><animate attributeName="y" calcMode="linear" values="-4;-22" keyTimes="0;1" dur="0.3333333333333333" begin="0s" repeatCount="indefinite"></animate></use></g><path d="M81.2,52.5l-5.2,0V49c0-1.6-1.3-3-3-3H20c-1.6,0-3,1.3-3,3v11.6C17,71.3,25.7,80,36.5,80h20.1 c7.1,0,13.3-3.8,16.7-9.5h8.3c5.2,0,9.3-4.4,9-9.6C90.2,56.1,86,52.5,81.2,52.5z M81.5,67.5h-6.8c0.8-2.2,1.3-4.5,1.3-7v-5h5.5 c3.3,0,6,2.7,6,6S84.8,67.5,81.5,67.5z" fill="#da3732"></path><path d="M78.8,88H19.2c-1.1,0-2-0.9-2-2s0.9-2,2-2h59.5c1.1,0,2,0.9,2,2S79.9,88,78.8,88z" fill="#e4762f"></path></svg>
            <span>正在加载</span>
           </span>
            <span v-else-if="loadable">上拉加载更多呦^.^</span>
            <span v-else>已经到底了-.-</span>
        </div>
    </div>
</template>
<script type="text/babel">
  export default {
    name: 'loadMore',
    data() {
      return {
        /**
         * 总页数（由服务端返回）
         * @type {number}
         */
        count: 0,
        /**
         * 是否正在拖拽中
         * @type {boolean}
         */
        dragging: false,
        /**
         * 已加载次数
         * @type {number}
         */
        times: 0,
        /**
         * 已开始记载
         * @type {boolean}
         */
        started: false,
        /**
         * 正在加载中
         * @type {boolean}
         */
        loading: false,
      }
    },
    props: {
      /**
       * 初始化后自动开始加载数据
       */
      autoload: {
        type: Boolean,
        default: true,
      },
      /**
       * 离组件最近的可滚动父级元素（用于监听事件及获取滚动条位置）
       */
      container: {
        // Selector or Element
        default: 'body',
      },
      /**
       * 禁用组件
       */
      disabled: {
        type: Boolean,
        default: false,
      },
      /**
       * 起始页码
       */
      page: {
        type: Number,
        default: 1,
      },
      /**
       * 每页加载数据条数
       */
      rows: {
        type: Number,
        default: 10,
      },
      /**
       * 数据加载请求地址
       */
      url: {
        type: String,
        default: '',
      },
      /**
       * 额外参数
       */
      params: {
        type: Object,
        default: Object,
      },
      /**
       * http 处理参数的额外参数
       * */
      analysis:{
        type:Object,
        default:Object
      }
    },
    computed: {
      /**
       * 是否可以加载
       * @returns {boolean} 是与否
       */
      loadable() {
        return !this.disabled && (!this.started || (this.page + this.times) <= this.count)
      },
    },
    mounted() {
      let container = this.container
      if (container) {
        if (typeof container === 'string') {
          container = document.querySelector(container)
        } else if (!container.querySelector) {
          container = document.body
        }
      }
      if (!container) {
        container = document.body
      }
      this.$container = container
      this.onPointerDown = this.pointerdown.bind(this)
      this.onPointerMove = this.pointermove.bind(this)
      this.onPointerUp = this.pointerup.bind(this)
      if (global.PointerEvent) {
        container.addEventListener('pointerdown', this.onPointerDown, false)
        container.addEventListener('pointermove', this.onPointerMove, false)
        container.addEventListener('pointerup', this.onPointerUp, false)
        container.addEventListener('pointercancel', this.onPointerUp, false)
      } else {
        container.addEventListener('touchstart', this.onPointerDown, false)
        container.addEventListener('touchmove', this.onPointerMove, false)
        container.addEventListener('touchend', this.onPointerUp, false)
        container.addEventListener('touchcancel', this.onPointerUp, false)
        container.addEventListener('mousedown', this.onPointerDown, false)
        container.addEventListener('mousemove', this.onPointerMove, false)
        container.addEventListener('mouseup', this.onPointerUp, false)
      }
      if (this.autoload) {
        this.load()
      }
    },
    // eslint-disable-next-line
    beforeDestroy() {
      const container = this.$container
      if (global.PointerEvent) {
        container.removeEventListener('pointerdown', this.onPointerDown, false)
        container.removeEventListener('pointermove', this.onPointerMove, false)
        container.removeEventListener('pointerup', this.onPointerUp, false)
        container.removeEventListener('pointercancel', this.onPointerUp, false)
      } else {
        container.removeEventListener('touchstart', this.onPointerDown, false)
        container.removeEventListener('touchmove', this.onPointerMove, false)
        container.removeEventListener('touchend', this.onPointerUp, false)
        container.removeEventListener('touchcancel', this.onPointerUp, false)
        container.removeEventListener('mousedown', this.onPointerDown, false)
        container.removeEventListener('mousemove', this.onPointerMove, false)
        container.removeEventListener('mouseup', this.onPointerUp, false)
      }
      if (this.loading && this.cancel) {
        this.cancel()
      }
    },
    methods: {
      /**
       * 加载一组数据的方法
       */
      load() {
        if (this.disabled || this.loading) {
          return
        }
        this.started = true
        this.loading = true
        const params = {
          ...{
            page: this.page + this.times,
            per_page: this.rows,
          },
          ...this.params
        };
        this.$http(this.url, params,this.analysis).then((res) => {
          const data = res.data
          this.times += 1
          this.loading = false
          this.count = res.meta.pagination.total_pages
          this.$emit('success', data)
          this.$emit('complete')
        }).catch((e) => {
          this.loading = false
          this.$emit('error', e)
          this.$emit('complete')
        });
      },
      /**
       * 重置加载相关变量
       */
      reset() {
        this.count = 0
        this.times = 0
        this.started = false
        this.loading = false
      },
      /**
       *重新开始加载
       */
      restart() {
        this.reset();
        this.load();
      },
      /**
       * 鼠标按下事件处理函数
       * @param {Object} e - 事件对象
       */
      pointerdown(e) {
        if (this.disabled || !this.loadable || this.loading) {
          return
        }
        this.dragging = true
        this.pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY
      },
      /**
       * 鼠标移动事件处理函数
       * @param {Object} e - 事件对象
       */
      pointermove(e) {
        if (!this.dragging) {
          return
        }
        const container = this.$container
        const pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY
        const moveY = pageY - this.pageY
        // 如果已经向下滚动到页面最底部
        if (moveY < 0 && (container.scrollTop + Math.min(
          global.innerHeight,
          container.clientHeight,
        )) >= container.scrollHeight) {
          // 阻止原生的上拉拖动会露出页面底部空白区域的行为（主要针对iOS版微信）
          e.preventDefault()
          // 如果上拉距离超过50像素，则加载下一页
          if (moveY < 50) {
            this.pageY = pageY
            this.load()
          }
        }
      },
      /**
       * 鼠标松开事件处理函数
       */
      pointerup() {
        this.dragging = false
      },
    },
  }
</script>
