<template>
    <div class="item">
        <div class="head">
            <img src="../../assets/img/shop/ic_shop_small.png" alt="">
            <span class="name">{{data.shop.name}}</span>
            <span class="info">{{data.status|info}}</span>
        </div>
        <router-link :to="{name:'orderDetails',query:{order_id:data.order_id}}" class="message">
            <div v-for="(item,index) in data.items" :key="index">
                <div class="left">
                    <img
                        :src="item.image"
                        alt=""/>
                </div>
                <div class="right">
                    <h1>
                        {{item.name}}
                    </h1>
                    <p>{{item.attr}}</p>
                    <div class="money">
                        <span>￥{{item.price}}</span>
                        <span>原价:￥{{item.original_price}}</span>
                        <span>x{{item.amount}}</span>
                    </div>
                </div>
            </div>
        </router-link>
        <div class="footer">
            <div class="left">
                <span>运费</span>
                <span>￥{{data.ship_fee}}</span>
            </div>
            <div class="right">
                <!--                <div v-if="data.status===200" @click="setorder(210)">发货</div>-->
                <div v-if="data.status===100" @click="setorder(900)">取消订单</div>
                <div v-if="data.status===100" @click="goPay" class="active">支付订单</div>
                <router-link v-if="data.status===230"
                             :to="{name:'orderComplain',query:{order_id:data.order_id,type:20}}">投诉订单
                </router-link>
                <div v-if="data.status===210" @click="setorder(220)" class="active">确认收货</div>
                <div v-if="data.status===230" @click="shopEvaluate" class="active">
                    去评价
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapActions, mapState} from "vuex";

  export default {
    name: "shopOrderItem",
    props: ['data', '_this'],
    filters: {
      info(value) {
        //100 待支付；200 已支付 待发货；210 已发货待收货；220 已收货；230 已收货待评价；300 申请退款；310 退款中；320退款完成；330 退款拒绝；900 订单关闭；990 订单被删除
        switch (value) {
          case 100:
            return '待付款';
          case 200:
            return '待发货';
          case 210:
            return '待收货';
          case 230:
            return '待评价';
          case 240:
            return '已完成';
          case 300:
            return '申请退款';
          case 310:
            return '退款中';
          case 320:
            return '退款完成';
          case 330:
            return '退关拒绝';
          case 900:
            return '已取消';
          case 990:
            return '订单删除';
          default:
            return '未知'
        }
      }
    },
    beforeMount(){
      this.getBalance();
    },
    computed:{
      ...mapState({
        balance:state=>state.user.balance
      })
    },
    methods: {
      ...mapActions([
        'getBalance'
      ]),
      goPay() {
        let params = {
          target_type: 'store',
          target_ids: this.data.items.map(item => {
            return item.order_id
          }),
          payment_method: '',
        };
        this.$payShow(this.data.total_amount,this.balance, (payment_method) => {
          params.payment_method = payment_method;
          this.$http('pay', params).then(() => {
            this.$navigation.push(this, {
              name: 'success', query: {
                name:'orderDetails',
                order_id:params.target_ids[0]
              }
            });
          })
        });
      },
      setorder(status) {
        //220确认收货；900订单取消；990删除订单
        this.$http('setOrderStatus', {status: status}, {order_id: this.data.order_id}).then(() => {
          this.$emit('restart');
        })
      },
      shopEvaluate() {
        const data = {
          order_id: this.data.order_id,
          comments: this.data.items.map(item => {
            return {
              sku_id: item.product_sku_id,
              product_id: item.product_id,
              grade: 5,
              order_item_id: item.item_id,
              logo: item.image,
              images: [],
              content: '',
            }
          })
        };
        this.$navigation.push(this, {name: 'shopEvaluate', params: {data: data}})
      }
    }
  }
</script>

