    <div class="content_top enterShop">
        <nav-bar title="商城申请入驻" right="入驻须知" :right-callback="go"/>
        <div v-if="flag" class="noData">
            <img src="../../../assets/img/user/enter/pic_default_mall_entry.png" alt="">
            <p>入驻成功</p>
        </div>
        <div v-else class="body">
            <div class="uploadHeadImg">
                <h1>
                    上传店铺照片
                    <!--                    <span>(上限3张）</span>-->
                </h1>
                <div class="box">
                    <div class="upload">
                        <img v-if="logos" :src="logos" alt=""
                             style="width:100%;height:100%;margin: 0;padding:0;object-fit: cover;">
                        <img v-else src="../../../assets/img/user/enter/ic_national_emblem.png" alt="">
