                        <img :src="id_front||require('../../../assets/img/user/enter/ic_portrait_plane.png')" alt="">
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="id_front"
                               @change="uploadImg($event)"/>
                    </div>
                    <div class="uploadCard">
                        <img :src="id_back||require('../../../assets/img/user/enter/ic_national_emblem.png')" alt="">
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="id_back"
                               @change="uploadImg($event)"/>
                    </div>
                </div>
                <div class="foot">
                    <div>点击上传<span>人像面</span></div>
                    <div>点击上传<span>国徽面</span></div>
