                        <img v-if="!business_license" src="../../../assets/img/user/enter/ic_national_emblem.png"
                             alt="">
                        <p v-if="!business_license">添加照片</p>
                        <input type="file" accept="image/png,image/jpg,image/jpeg" data-type="business_license"
                               @change="uploadImg($event)"/>
                    </div>
                </div>
            </div>
            <div class="btn" @click="submit" v-show="this.status!=='pending'">
                <div class="active">确认入驻</div>
            </div>
        </div>
