</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "enterShop",
    data() {
      return {
        status: '',
        flag: false,
        logos: '',
        id_front: '',
        id_back: '',
        business_license: '',
        params: {
          name: '',//店铺名称
          logo: '',//店铺图片
          introduce: '',//店铺介绍
          contact: '',//联系人
          telphone: '',//联系电话
          id_front: '',//身份证人头像
          id_back: '',//身份证国徽面
          company_name: '',//公司名称
          address: '',//公司地址
          business_scope: '',//业务范围
          business_license: '',//上传营业执照
          shop_type: 10,//10商家 20服务商
        }
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo
      })
    },
    async beforeMount() {
      //pending审核中；failed审核失败；success审核成功
      const res = await this.$http('checkInfo');
      const {store_shop: data} = res;
      if (data.shop_type === 10) {
        this.status = data.status;
        //判断是否提交过数据
        if (data.name) {
          this.logos = data.logo;
          this.id_front = data.id_front;
          this.id_back = data.id_back;
          this.business_license = data.business_license;
          this.params = {
            name: data.name,
            logo: data.logo,//店铺图片
            introduce: data.introduce,
            contact: data.contact,//联系人
            telphone: data.telphone,//联系电话
            id_front: data.id_front,//身份证人头像
            id_back: data.id_back,//身份证国徽面
            company_name: data.company_name,//公司名称
            address: data.address,//公司地址
            business_scope: data.business_scope,//业务范围
            business_license: data.business_license,//上传营业执照
            shop_type: 10,//10商家 20服务商
            user_remark: ''
          };
        }
        if (data.status === 'pending') {
          return this.$alert('审核中');
        } else if (data.status === 'failed') {
          return this.$prompt('审核不通过', data.remark, () => {
            this.$http('shopRest',{shop_type:10});
          }, '重新申请');
        } else if (data.status === 'success') {
          return this.flag = true;
        }
      }
    },
    methods: {
      ...mapActions(['setUserInfo']),
      go() {
        this.$navigation.push(this, {name: 'enterNotice'})
      },
      uploadImg($event) {
        let formData = new FormData();
        const self = $event.target;
        const type = self.dataset.type;
        formData.append('file', self.files[0]);
        this.$http('upload', formData).then(res => {
          if (type === 'logo') {
            this.logos = res.data.visit_url;
            this.params.logo = res.data.visit_url;
          } else {
            this[type] = res.data.visit_url;
            this.params[type] = res.data.url;
          }
        });
      },
      submit() {
        if (!this.params.logo) {
          return this.$toast('请上传店铺图片')
        }
        if (!this.params.name) {
          return this.$toast('请输入店铺名字')
        }
        if (this.params.name.length > 15) {
          return this.$toast('店铺名字最长只能15位')
        }
        if (this.params.name.match(/^[0-9a-zA-Z\u4e00-\u9fa5]+$/) === null) {
          return this.$toast('店铺名字只能是数字和字母')
        }
        if (!this.params.introduce) {
          return this.$toast('请输入店铺介绍')
        }
        if (!this.params.contact) {
          return this.$toast('请输入联系人')
        }
        if (!this.params.telphone) {
          return this.$toast('请输入联系人电话')
        }
        if (!(/^1[3456789]\d{9}$/.test(this.params.telphone))) {
          return this.$alert("联系人手机号码不正确");
        }
        if (!this.params.id_front) {
          return this.$toast('请上传身份证正面')
        }
        if (!this.params.id_back) {
          return this.$toast('请上传身份证反面')
        }
        if (!this.params.company_name) {
          return this.$toast('请输入公司名称')
        }
        if (!this.params.address) {
          return this.$toast('请输入公司地址')
        }
        if (!this.params.business_scope) {
          return this.$toast('请输入业务范围')
        }
        if (!this.params.business_license) {
          return this.$toast('请上传营业执照')
        }

        this.$http('addShop', this.params).then(() => {
          this.$toast('提交成功');
          this.$navigation.back(this);
        })
      }
    }
  }
</script>

