<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "orderDetails",
    data() {
      return {
        info: {
          address: {
            contact_phone: ''
          },
          shop: {},
          items: [],
          receipt: {}
        },
        params: {
          target_type: 'store',
          target_ids: [],
          payment_method: 'balance',
        }
      }
    },
    computed:{
      ...mapState({
        balance:state=>state.user.balance
      })
    },
    beforeMount() {
      this.getBalance();
      this.getInfo();
    },
    methods: {
      ...mapActions([
        'getBalance'
      ]),
      getInfo() {
        this.$http('shopOrderDetails', {}, {order_id: this.$route.query.order_id}).then(res => {
          this.info = res.data;
          this.params.target_ids = res.data.items.map(item => {
            return item.order_id
          })
        })
      },
      shopEvaluate() {
        const data = {
          order_id: this.info.order_id,
          comments: this.info.items.map(item => {
            return {
              sku_id: item.product_sku_id,
              product_id: item.product_id,
              grade: 5,
              order_item_id: item.id,
              logo: item.product.image,
              images: [],
              content: '',
            }
          })
        };
        this.$navigation.push(this, {name: 'shopEvaluate', params: {data: data}})
      },
      goPay() {
        this.$http('pay', this.params).then(() => {
          // this.getInfo();
          this.$navigation.push(this, {
            name: 'success', query: {
              name:'orderDetails',
              order_id:this.params.target_ids[0]
            }
          });
        })
      },
      setorder(status) {
        //220确认收货；900订单取消；990删除订单
        this.$http('setOrderStatus', {status: status}, {order_id: this.info.id}).then(() => {
          if (status === 990) {
            this.$navigation.back(this);
          } else {
            this.getInfo();
          }
        })
      }
    }
  }
</script>

