import {SET_NOTICE_DATA} from './types';
import Http from "../../../tools/http";

const state = {
  home_notice:[],//首页公告
};
const getters = {};
const actions = {
  async getHomeNotice(context) {
    const data = await Http('notice');
    context.commit(SET_NOTICE_DATA, {key: 'home_notice', data: data.data});
  },
};
const mutations = {
  [SET_NOTICE_DATA](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
