import {SET_SITE_INFO} from './types';
import Http from "../../../tools/http";

const state = {
  list:[]
};
const getters = {};
const actions = {
  async getSiteList(context, params) {
    let data = await Http('addresses', params);
    context.commit(SET_SITE_INFO,{key:'list',data:data.data});
  },
};
const mutations = {
  [SET_SITE_INFO](state, params) {
    state[params.key]=params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
