<template>
    <div class="content_top editSite">
        <nav-bar :title="title" right="完成" :right-callback="()=>{submit()}"></nav-bar>
        <div class="body">
            <div class="item">
                <span>收货人</span>
                <input type="text" v-model="params.contact_name" placeholder="请输入收货人" value="晓晓">
            </div>
            <div class="item">
                <span>手机号码</span>
                <input type="number" v-model="params.contact_phone" placeholder="请输入手机号码" value="晓晓">
            </div>
            <div class="item" @click="siteShow">
                <span>所在地址</span>
                <input type="text" readonly="readonly" placeholder="请选择地址"
                       :value="`${params.province}${params.city}${params.district}`">
                <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
            </div>
            <div class="item text">
                <span>详细地址</span>
                <textarea placeholder="请输入详细地址" v-model="params.address"></textarea>
            </div>
            <div class="defaultSite">
                <div class="switch" :class="params.default&&'active'" @click="params.default=params.default?0:1"></div>
                <h1>
                    设置默认地址
                </h1>
                <div class="info">
                    提醒：每次下单时会使用该地址为默认地址，请在下单时确认哦！
                </div>
            </div>
            <div v-if="$route.query.flag" class="btn" @click="del">
                删除
            </div>
        </div>
    </div>
</template>

<script>
  import IosSelect from 'iosselect';
  import * as site from '../../../conf/areaData';
  import {mapActions} from 'vuex';

  export default {
    name: "editSite",
    data() {
      return {
        params: {
          contact_name: '',
          contact_phone: '',
          default: 0,
          province: '',
          city: '',
          district: '',
          address: ''
        }
      }
    },
    beforeMount() {
      if (this.$route.query.flag) {
        this.title = '编辑地址';
        this.params = {...this.$route.params};
      } else {
        this.title = '添加新地址';
      }
    },
    methods: {
      ...mapActions(['getSiteList']),
      del() {
        this.$http('delAddress', {}, {'address_id': this.params.address_id}).then(() => {
          this.$toast('地址删除成功', () => {
            this.getSiteList().then(() => {
              this.$navigation.back(this);
            });
          })
        })
      },
      submit() {
        if (!this.params.contact_name) {
          return this.$toast('请输入收货人');
        }
        if (!this.params.contact_phone) {
          return this.$toast('请输入联系方式');
        }
        if (!this.params.province || !this.params.city || !this.params.district) {
          return this.$toast('请选择收货地址');
        }
        if (!this.params.address) {
          return this.$toast('请输入详细地址');
        }
        if (this.$route.query.flag) {
          this.$http('setAddress', this.params, {'address_id': this.params.address_id}).then(() => {
            this.$toast('地址修改成功', () => {
              this.getSiteList().then(() => {
                this.$navigation.back(this);
              });
            })
          })
        } else {
          this.$http('addAddresses', this.params).then(() => {
            this.$toast('地址添加成功', () => {
              this.getSiteList().then(() => {
                this.$navigation.back(this);
              });
            })
          })
        }

      },
      siteShow() {
        new IosSelect(3,
          [site.iosProvinces, site.iosCitys, site.iosCountys],
          {
            title: '地址选择',
            itemHeight: 35,
            relation: [1, 1],
            oneLevelId: '河南省',
            twoLevelId: '郑州市',
            // threeLevelId: '',
            callback: (selectOneObj, selectTwoObj, selectThreeObj) => {
              this.params.province = selectOneObj.value;
              this.params.city = selectTwoObj.value;
              this.params.district = selectThreeObj.value;
            }
          });
      }
    }
  }
</script>

