<template>
    <div class="content_top orderEvaluate">
        <nav-bar :title="$route.query.name" right="提交" :rightCallback="submit" ></nav-bar>
        <div class="body">
            <div class="box">
                <div class="top">
                    <div class="left">
                        <img
                            :src="$route.query.logo"
                            alt="">
                    </div>
                    <div class="right">
                        <div>
                            <span>服务态度</span>
                            <img v-for="n in total" v-if="n<=params.service_atti"
                                 src="../../../assets/img/home/ic_stars_small_yellow.png"
                                 @click="params.service_atti=n"/>
                            <img v-for="n in total" v-if="n>params.service_atti"
                                 src="../../../assets/img/home/ic_stars_small_gray.png" @click="params.service_atti=n"/>
                        </div>
                        <div>
                            <span>服务时间</span>
                            <img v-for="n in total" v-if="n<=params.service_time"
                                 src="../../../assets/img/home/ic_stars_small_yellow.png"
                                 @click="params.service_time=n"/>
                            <img v-for="n in total" v-if="n>params.service_time"
                                 src="../../../assets/img/home/ic_stars_small_gray.png" @click="params.service_time=n"/>
                        </div>
                        <div>
                            <span>服务质量</span>
                            <img v-for="n in total" v-if="n<=params.service_mass"
                                 src="../../../assets/img/home/ic_stars_small_yellow.png"
                                 @click="params.service_mass=n"/>
                            <img v-for="n in total" v-if="n>params.service_mass"
                                 src="../../../assets/img/home/ic_stars_small_gray.png" @click="params.service_mass=n"/>
                        </div>
                    </div>
                </div>
                <div class="center">
                    <textarea placeholder="请对该商品进行评价" v-model="params.content" @input="input"></textarea>
                </div>
                <div class="bottom">
                    <ul>
                        <li v-for="(item,index) in images" :key="index">
                            <img :src="item" alt="">
                        </li>
                        <li>
                            <img src="../../../assets/img/shop/ic_add_pictures.png" alt="">
                            <input type="file" @change="uploadImg($event)">
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "orderEvaluate",
    data() {
      return {
        images: [],
        total: 5,
        params: {
          order_id: this.$route.query.order_id,
          content: '',
          images: [],
          service_atti: 5,
          service_time: 5,
          service_mass: 5,
        }
      }
    },
    methods: {
      del(x) {
        const arr = this.images;
        const arr2 = this.params.images;
        arr.splice(x,1);
        arr2.splice(x,1);
        this.images = arr;
        this.params.images = arr2;
      },
      input(){
        if(this.params.content.length>=100){
          this.params.content=this.params.content.substr(0,100)
        }
      },
      uploadImg($event) {
        let formData = new FormData();
        const self = $event.target;
        formData.append('file', self.files[0]);
        this.$http('upload', formData).then(res => {
          this.images.push(res.data.visit_url);
          this.params.images.push(res.data.url);
        });
      },
      submit(){
        this.$http('serviceEvaluate',this.params).then(()=>{
          this.$navigation.back(this);
        })
      }
    }
  }
</script>

