<template>
    <div :class="'carousel'+type">
        <div class="swiper-container" :class="{home:type==1,haoyin:type==2,shop:type==3}">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index) in data" :key="index" @click="()=>go(item)">
                    <img v-if="type==3" :src="item"/>
                    <img v-else :src="item.cover"/>
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>
            <!--         如果需要导航按钮 -->
            <!--            <div class="swiper-button-prev"></div>-->
            <!--            <div class="swiper-button-next"></div>-->
            <!-- 如果需要滚动条 -->
            <!-- <div class="swiper-scrollbar"></div>-->
        </div>
    </div>
</template>
<script>
  import 'swiper/dist/css/swiper.css'
  import Swiper from 'swiper';

  export default {
    name: "carousel",
    props: ['type', 'data'],
    mounted() {
      let mySwiper = new Swiper(`.carousel${this.type}>div`, {
        // loop: true,
        autoplay: true,
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
        },
        // 如果需要前进后退按钮
        // 如果需要前进后退按钮
        // navigation: {
        //   nextEl: '.swiper-button-next',
        //   prevEl: '.swiper-button-prev',
        // },
        observer: true,//修改swiper自己或子元素时，自动初始化swiper
        observeParents: false,//修改swiper的父元素时，自动初始化swiper
        onSlideChangeEnd: function (swiper) {
          swiper.update();
          mySwiper.startAutoplay();
          mySwiper.reLoop();
        }
        // 如果需要滚动条
        // scrollbar: {
        //     el: '.swiper-scrollbar',
        // },
      })
    },
    methods: {
      go(item) {
        if(this.type!=3){
          if (!item.content){
            return window.location.href = item.cover;
          }
          this.$navigation.push(this, {
            name: 'bannerInfo',
            params: {
              data: item
            }
          })
        }
      }
    }
  }
</script>

<style scoped lang="less">
    .swiper-container {
        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }

        &.home {
            img {
                width: 100%;
                height: 232px;
            }
        }

        &.haoyin {
            img {
                width: 100%;
                height: 232px;
            }
        }

        &.shop {
            img {
                width: 100%;
                height: 750px;
            }
        }
    }
</style>
