<template>
    <div class="content_top login">
        <nav-bar title="单位注册" type="red"/>
        <div class="logo">
            <img src="" alt="">
        </div>
        <div class="form">
            <form-input icon="personal" type="text" v-model="params.name" placeholder="用户姓名" @input="this.input"/>
            <form-input icon="user" type="text" v-model="params.phone" placeholder="请输入手机号" @input="this.input"/>
            <form-input icon="code" type="text" v-model="params.verification_code" placeholder="短信验证码" code="true"
                        :smsText="time===0?'发送':time+'s'"
                        :callback="()=>{this.sms()}"
                        @input="this.input"
            />
            <form-input icon="pwd" type="password" v-model="params.password" placeholder="设置登录密码" @input="this.input"/>
            <div class="formInput">
                <div class="icon">
                    <img src="../../assets/img/icon/ic_login_unit_type.png" alt="">
                </div>
                <div class="input">
                    <select v-model="params.unit_type">
                        <option v-for="(item,index) in config.unit_type" :key="index" :value="item">{{item}}</option>
                    </select>
                </div>
            </div>
            <form-input icon="info" type="text" v-model="params.unit_name" placeholder="单位全称" @input="this.input"/>
            <form-input icon="shopCode" type="text" v-model="params.invite_code" placeholder="店铺推荐码"
                        @input="this.input"/>
        </div>
        <div class="agreement">
            <img src="../../assets/img/icon/ic_agree_selected_red.png" alt="" v-if="agree"
                 @click="()=>{this.agree=!this.agree;this.input()}">
            <img src="../../assets/img/icon/ic_agree_no_choose_gray.png" alt="" v-else
                 @click="()=>{this.agree=!this.agree;this.input()}">
            <span>我已阅读并同意</span>
            <router-link :to="{name:'agreement'}">《用户服务协议及隐私政策》</router-link>
        </div>
        <div class="loginBtn" :class="flag&&'active'" @click="submit">
            申请企业账号
        </div>
    </div>
</template>

<script>
  import {mapState} from 'vuex';

  export default {
    name: "unitReg",
    data() {
      return {
        flag: false,
        agree: false,
        time: 0,
        timeOut: null,
        params: {
          phone: '',
          verification_code: '',
          verification_key: '',
          password: '',
          invite_code: '',
          identify: 'unit',
          unit_type: '',
          unit_name: '',
          name: '',
        }
      }
    },
    beforeMount() {
      this.params.unit_type = this.config.unit_type[0]
    },
    computed: {
      ...mapState({
        config: state => state.config.config
      })
    },
    beforeDestroy() {
      this.timeOut && clearInterval(this.timeOut);
    },
    methods: {
      sms() {
        if (this.time > 0) {
          return this.$toast('请稍后发送');
        }
        if (!this.params.phone) {
          return this.$toast('手机号不能为空');
        }
        if(!(/^1[3456789]\d{9}$/.test(this.params.phone))){
          return this.$alert("手机号码有误，请重填");
        }
        this.$http('sms', {phone: this.params.phone}).then(res => {
          this.params.verification_key = res.data.key;
          this.time = 60;
          this.timeOut = setInterval(() => {
            this.time = this.time - 1;
            if (this.time === 0) {
              this.time = 0;
              clearInterval(this.timeOut);
            }
          }, 1000)
        });
      },
      input() {
        if (this.params.phone && this.params.verification_code && this.params.verification_key  && this.params.password && this.agree && this.params.unit_name && this.params.unit_type && this.params.name) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      },
      submit() {
        if (!this.flag) {
          return false;
        }
        if (!this.params.phone) {
          return this.$toast('手机号不能为空');
        }
        if (!this.params.verification_code) {
          return this.$toast('验证码不能为空');
        }
        if (!this.params.password) {
          return this.$toast('请输入密码');
        }
        // if (!this.params.invite_code) {
        //   return this.$toast('请输入店铺推荐码');
        // }
        this.$http('reg', this.params).then(() => {
          this.$alert('注册成功', () => {
            this.$navigation.push(this, {name: 'login'});
          })
        });
      }
    },
  }
</script>

