<template>
    <div class="content_top complain">
        <nav-bar title="意见反馈"></nav-bar>
        <div class="body">
            <div class="input">
                <div class="text">
                    <textarea v-model="params.content" @input="input" placeholder="请简要描述您的投诉问题"></textarea>
                </div>
                <div class="footer">
                    <span>{{params.content.length}}</span>/100
                </div>
            </div>
            <div class="info">
                <div class="item">
                    <div class="left">
                        投诉人姓名
                    </div>
                    <div class="right">
                        <input type="text" v-model="params.name" placeholder="请填写投诉人姓名">
                    </div>
                </div>
                <div class="item">
                    <div class="left">
                        投诉人电话
                    </div>
                    <div class="right">
                        <input type="text" v-model="params.mobile" placeholder="请填写联系电话">
                    </div>
                </div>
            </div>
            <div class="message">
                <b>友情提示：</b>
                <span>请确保提供的个人信息真实有效，否则我们可能无法处理您的投诉，谢谢！</span>
            </div>
            <div class="btn active" @click="submit">提交</div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "complain",
    data() {
      return {
        params: {
          content: '',
          name: '',
          mobile: '',
          order_id: this.$route.query.order_id,
          type: this.$route.query.type,
        }
      }
    },
    methods: {
      input() {
        if (this.params.content.length > 100) {
          this.params.content = this.params.content.substr(0, 100)
        }
      },
      submit(){
        this.$http('complain',this.params).then(()=>{
          this.$alert('投诉成功',()=>{
            this.$navigation.back(this);
          })
        })
      }
    }
  }
</script>

