import {SET_CAROUSEL_DATA} from './types';
import Http from "../../../tools/http";

const state = {
  carousel: [],
  carousel_shop: []
};
const getters = {};
const actions = {
  async getCarouselShop(context) {
    const carouselData = await Http('carousel')
    const data = await Http('carouselShop')
    context.commit(SET_CAROUSEL_DATA,{
      key:'carousel',
      data:carouselData.advertisement_items,
    });
    context.commit(SET_CAROUSEL_DATA,{
      key:'carousel_shop',
      data:data.advertisement_items,
    })
  },
};
const mutations = {
  [SET_CAROUSEL_DATA](state, params) {
    state[params.key]=params.data
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
