<template>
    <div class="content_top setting">
        <nav-bar title="设置"/>
        <div class="body">
            <div class="links">
                <router-link :to="{name:'settingOpinion'}" class="item">
                    <div class="left">
                        意见反馈
                    </div>
                    <div class="right">
                        <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                    </div>
                </router-link>
                <div class="item" @click="call">
                    <div class="left">
                        客服电话
                    </div>
                    <div class="right">
                        020-400-400
                    </div>
                </div>
                <div class="item">
                    <div class="left">
                        当前版本
                    </div>
                    <div class="right">
                        v1.0
                    </div>
                </div>
            </div>
            <div class="btn" @click="loginOut" v-if="token">
                退出登录
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "settingIndex",
    computed: {
      ...mapState({
        token: state => state.user.token.access_token
      })
    },
    methods: {
      ...mapActions([
        'clearUser'
      ]),
      call() {
        this.$call();
      },
      loginOut() {
        this.$confirm('您确定退出登录吗?', () => {
          localStorage.clear();
          this.clearUser().then(()=>{
            this.$navigation.replace(this,{name:'login'});
          });
        })
      }
    }
  }
</script>

