<template>
    <div class="haoyin content" ref="appBox">
        <haoyin-search-bar/>
        <nav-foot/>
        <carousel :data="carousel" type="2"/>
        <div class="btns">
            <router-link
                :to="item.name==='其他'?{name:'classify'}:{name:'haoyinSearch',query:{category_id:item.id,name:item.name}}"
                v-for="(item,index) in menu" :key="index">
                <div class="icon">
                    <img :src="item.image" alt="">
                </div>
                <div class="info">{{item.name}}</div>
            </router-link>
        </div>
        <div class="tjTitle">
            <h1>商品推荐</h1>
        </div>
        <div class="list">
            <item v-for="(item,index) in list" :data="item" :key="index" type="2"/>
            <load-more
                url="recommendShop"
                @success="success"
                @complete="complete"
            ></load-more>
        </div>
    </div>
</template>

<script>
  import HaoyinSearchBar from "../../components/haoyin/searchBar";
  import Carousel from "../../components/home/carousel";
  import Item from "../../components/shop/item";
  import LoadMore from "../../components/public/loadMore";

  import {mapState, mapActions} from 'vuex';

  export default {
    name: "haoyinIndex",
    data() {
      return {
        list: [],
      }
    },
    components: {LoadMore, Item, Carousel, HaoyinSearchBar},
    beforeMount() {
      this.getMenu()
      this.getCarouselShop()
      // this.getCommendShop(params);
    },
    computed: {
      ...mapState({
        carousel: state => state.carousel.carousel_shop,
        menu: state => state.shop.menu
      })
    },
    beforeRouteLeave(to, from, next) {
      next()
    },
    methods: {
      ...mapActions([
        'getMenu',
        'getCarouselShop'
      ]),
      success(data) {
        this.list = this.list.concat(data);
      },
      complete() {

      }
    }
  }
</script>

