<script>
  //订单状态；10 默认 待确认；20 已确认，待付款；30 已付款，待收货；40 已收货，待评价；50 已完成
  import {mapActions, mapState} from "vuex";

  export default {
    name: "orderDetails",
    data() {
      return {
        info: {
          address: {
            receive_phone: '',
          },
          cate_info: {},
          receipt_info: {},
          shop_info: {},
        }
      }
    },
    computed:{
      ...mapState({
        balance:state=>state.user.balance
      })
    },
    beforeMount() {
      this.getBalance();
      this.getInfo();
    },
    methods: {
      ...mapActions([
        'getBalance'
      ]),
      go(data){
        this.$navigation.push(this,data)
      },
      getInfo() {
        this.$http('serviceOrderDetails', {}, {order_id: this.$route.query.order_id}).then(res => {
          this.info = res.data;
        })
      },
      goPay() {
        let params = {
          target_type: 'store_service',
          target_id: this.info.order_id,
          payment_method: '',
        };
        this.$payShow(this.info.total_amount,this.balance, (payment_method) => {
          params.payment_method = payment_method;
          this.$http('pay', params).then(() => {
            // this.getInfo();
            this.$navigation.push(this, {
              name: 'success', query: {
                name:'details',
                order_id:params.target_id
              }
            });
          })
        });
      },
      setorder(status) {
        //220确认收货；900订单取消；990删除订单
        this.$http('serviceOrderStatus', {status: status}, {order_id: this.info.order_id}).then(() => {
          if (status === 990) {
            this.$navigation.back(this);
          } else {
            this.getInfo();
          }
        })
      }
    }

  }
</script>

