import {SET_CONFIG_DATA} from './types';
import Http from "../../../tools/http";

const state = {
  config: {},
  map: {
    lng: 0,
    lat: 0,
    address: [],
    site:''
  },
  search: {
    sort: 'distance',
    service_id: '',
    keyword: '',
    flag:false,
  },
};
const getters = {};
const actions = {
  async getConfig(context) {
    let data = await Http('config');
    await context.commit(SET_CONFIG_DATA, {key: 'config', data: data.data});
  },
  async setSearch(context, params) {
    await context.commit(SET_CONFIG_DATA, {key: 'search', data: params})
  },
  async setMap(context, params) {
    await context.commit(SET_CONFIG_DATA, {
      key: 'map',
      data: {
        ...state.map,
        ...params
      }
    })
  },
};
const mutations = {
  [SET_CONFIG_DATA](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
