<template>
    <div class="content_top putMake">
        <nav-bar title="填写信息"></nav-bar>
        <div class="body">
            <div class="site">
                <div class="noSite" v-if="!this.site"
                     @click="()=>{this.$navigation.push(this, {name: 'siteIndex'})}">
                    <img src="../../../assets/img/shop/make/ic_add_address.png" alt="">
                    <span>添加收货人信息</span>
                </div>
                <div class="info" v-else
                     @click="()=>{this.$navigation.push(this, {name: 'siteIndex'})}"
                >
                    <div class="one">
                        <div class="left">收货人：{{site.contact_name}}</div>
                        <div class="right">{{site.contact_phone.substr(0,4)+'****'+site.contact_phone.substr(8,11)}}
                        </div>
                    </div>
                    <div class="two">
                        <div><img src="../../../assets/img/shop/ic_shop_address.png" alt=""></div>
                        <span>{{site.province}}{{site.city}}{{site.district}}{{site.address}}</span>
                    </div>
                </div>
            </div>
            <div class="info">
                <div class="box1" @click="timeShow">
                    <div class="left">
                        {{$route.query.type===20?'预约上门时间':'送货时间'}}
                    </div>
                    <div class="right">
                        <span>{{this.params.time||'选择时间'}}</span>
                        <img src="../../../assets/img/shop/ic_selection_date.png" alt="">
                    </div>
                </div>
                <div class="box2" v-if="!$route.query.type">
                    <div class="left">
                        打印页数
                    </div>
                    <div class="right">
                        <div class="one">
                            <input type="text" v-model="params.page_nums">
                            <span>页</span>
                        </div>
                        <div class="two"></div>
                        <div class="three">份数</div>
                        <div class="four">
                            <input type="text" v-model="params.copy_nums">
                            <span>份</span>
                        </div>
                    </div>
                </div>
                <div class="box3" @click="pageSize" v-if="!$route.query.type">
                    <div class="left">
                        纸张大小
                    </div>
                    <div class="right">
                        <span>{{params.size_name||'选择纸张'}}</span>
                        <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                    </div>
                </div>
            </div>
            <div class="message">
                <div @click="go" class="top">
                    <h1>发票</h1>
                    <span>{{params.receipt_type===10?'电子普通发票':params.receipt_type===20?'纸质普通发票':''}}</span>
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </div>
                <div class="textarea">
                    <h1>备注</h1>
                    <textarea placeholder="请填写你需要的备注" v-model="params.description" @input="input"></textarea>
                </div>
                <div class="foot">
                    <span>{{params.description.length}}</span>/100
                </div>
            </div>
        </div>
        <div class="footer active" @click="submit">
            提交预约
        </div>
        <div class="container"></div>
    </div>
</template>

<script>
  import IosSelect from 'iosselect';
  import * as Fun from '../../../tools/functions';
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "putMake",
    data() {
      return {
        params: {
          receipt_type: 0,
          time: '',
          shop_id: this.$route.query.shop_id,
          cate_id: this.$route.query.cate_id,
          address_id: '',
          page_nums: 1,
          copy_nums: 1,
          size_name: '',
          receipt_id: '',
          description: '',
          deliver_time: '',
          type: this.$route.query.type || 10,
        }
      }
    },
    computed: {
      ...mapState({
        site: state => state.site.list.find(item => {
          return item.default === true
        }),
        global: state => state.global
      })
    },
    beforeMount() {
      this.getSiteList();
      this.params = {
        ...this.params,
        ...this.global.putMake,
        address_id: this.site.address_id,
      }
    },
    methods:
      {
        ...mapActions(['getSiteList', 'getGlobal']),
        go() {
          this.getGlobal({
            key: 'putMake',
            data: {
              ...this.params
            }
          });
          this.$navigation.push(this, {
            name: 'invoice',
            query: {name: 'putMake'},
          });
        },
        timeShow() {
          let nowTime = new Date();
          new IosSelect(5,
            [Fun.yearData, Fun.monthData, Fun.dateData, Fun.hourData, Fun.minuteData],
            {
              container: '.container',
              title: '时间选择',
              itemHeight: 35,
              itemShowCount: 9,
              oneLevelId: nowTime.getFullYear(),
              twoLevelId: nowTime.getMonth() + 1,
              threeLevelId: nowTime.getDate(),
              callback: (selectOneObj, selectTwoObj, selectThreeObj, selectFourObj, selectFiveObj) => {
                this.params.time = selectOneObj.value + '' + selectTwoObj.value + '' + selectThreeObj.value + ' ' + selectFourObj.value + '' + selectFiveObj.value;
                this.params.deliver_time = parseInt(selectOneObj.value) + '/' + parseInt(selectTwoObj.value) + '/' + parseInt(selectThreeObj.value) + ' ' + parseInt(selectFourObj.value) + ':' + parseInt(selectFiveObj.value);

              }
            });
        },
        pageSize() {
          this.$selectShow({
            data: [
              {title: 'A4', value: 'A4'},
              {title: 'A3', value: 'A3'},
            ], cancel: true, callback: value => {
              this.params.size_name = value
            },
          });
        },
        input() {
          if (this.params.description.toString().length >= 100) {
            this.params.description = this.params.description.substr(0, 100)
          }
        },
        submit() {
          if (!this.params.deliver_time) {
            return this.$toast('请选择时间');
          }
          if (!this.params.description) {
            return this.$toast('描述不能为空');
          }
          let params = this.params;
          if (this.$route.query.type == 20) {
            params = {
              address_id: params.address_id,
              cate_id: params.cate_id,
              deliver_time: params.deliver_time,
              description: params.description,
              receipt_id: params.receipt_id,
              shop_id: params.shop_id,
              type: 20
            }
          }
          params.address_id = this.params.address_id||this.site.address_id;
          params.receipt_id = this.params.receipt_id;

          if(!this.params.receipt_id){
            return this.$toast('请选择发票');
          }

          this.$http('makeService', params).then(res => {
            this.$navigation.push(this, {name: 'details', query: {order_id: res.data.order_id}})
          })
        }
      }
  }
</script>

