<template>
    <div class="content_top input">
        <nav-bar title="修改昵称" right="保存" :right-callback="submit"/>
        <div class="body">
            <input type="text" placeholder="请输入新昵称" v-model="nickname"/>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "infoInput",
    data() {
      return {
        nickname: ''
      }
    },
    beforeMount(){
      this.nickname=this.userInfo.nickname;
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo
      })
    },
    methods: {
      ...mapActions(['setUserInfo']),
      submit() {
        if (!this.nickname) {
          return this.$toast('请输入昵称')
        }
        this.$http('setUserInfo', {
          nickname: this.nickname,
        }).then(re => {
          this.setUserInfo(re.data).then(() => {
            this.userInfo.nickname=this.nickname;
            this.$navigation.back(this);
          });
        })
      },
    }
  }
</script>

<style scoped lang="less">
    .input {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #fff;

        .body {
            input {
                display: block;
                margin: 64px auto 0;
                width: 690px;
                height: 83px;
                line-height: 83px;
                border-bottom: 2px solid #666666;
                font-size: 29px;
                color: #000;
            }
        }
    }
</style>
