<template>
    <transition name="slide">
        <div class="service" v-show="flag">
            <div class="body">
                <h1>
                    服务类型选择
                    <img src="../../assets/img/cart/ic_circular_close.png" alt="" @click="flag=false">
                </h1>
                <div class="info">
                    <div>
                        <ul>
                            <li :class="index===oneIndex&&'active'" v-for="(item,index) in data" :key="index"
                                @click="()=>{selectIndex('oneIndex',index)}">{{item.name}}
                            </li>
                        </ul>
                    </div>
                    <div>
                        <ul>
                            <li :class="index===twoIndex&&'active'" v-for="(item,index) in twoArr"
                                :key="index" @click="()=>{selectIndex('twoIndex',index)}">{{item.name}}
                            </li>
                        </ul>
                    </div>
                    <div>
                        <ul>
                            <li :class="index===threeIndex&&'active'"
                                v-for="(item,index) in threeArr"
                                :key="index" @click="()=>{selectIndex('threeIndex',index)}">{{item.name}}
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="btn">
                    <div @click="this.rest">重置</div>
                    <div @click="this.submit">确定</div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
  export default {
    name: "service",
    data() {
      return {
        flag: false,
        data: [],
        oneIndex: 0,
        twoIndex: 0,
        threeIndex: 0,
        twoArr: [],
        threeArr: [],
      }
    },
    watch: {
      data() {
        this.selectIndex('oneIndex',0)
      }
    },
    methods: {
      selectIndex(type, index) {
        if (type === 'oneIndex') {
          this.twoIndex = 0;
          try {
            this.twoArr = this.data[index].subs;
          } catch (e) {
            this.twoArr = [];
          }
        }
        if (type === 'twoIndex') {
          this.threeIndex = 0;
          try {
            this.threeIndex = this.data[this.oneIndex].subs[index].subs;
          } catch (e) {
            this.threeIndex = [];
          }
        }
        this[type] = index;
      },
      submit() {
        let data;
        if(this.data[this.oneIndex]){
          data=this.data[this.oneIndex];
          if(data.subs[this.twoIndex]){
            data=data.subs[this.twoIndex];
            if(data.subs[this.threeIndex]){
              data=data.subs[this.threeIndex];
            }
          }
        }
        this.flag=false;
        this.callback&&this.callback(data);
      },
      rest() {
        this.oneIndex = 0;
        this.twoIndex = 0;
        this.threeIndex = 0;
      }
    }
  }
</script>

