import {SET_GLOBAL_DATA} from "./type";

const state = {
  putMake: {},//服务商的发票
  putMakeOrder: [],//商品发票
  visit: {},//好印上门定位
  serve: {},//入驻服务商定位

};
const actions = {
  async getGlobal(context, params) {
    await context.commit(SET_GLOBAL_DATA, params)
  },
};
const mutations = {
  [SET_GLOBAL_DATA](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  mutations,
  actions
}
