<template>
    <transition name="slide">
        <div class="propSelect" v-show="flag">
            <div class="body">
                <div class="title"></div>
                <div class="items">
                    <div v-for="item in list" :key="item.id" @click="ok(item.value)" class="item">{{item.title}}</div>
                </div>
                <div v-if="cancel" class="btn">
                    <div class="item" @click="()=>{this.flag=false}">取消</div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "propSelect",
        data() {
            return {
                flag: false,
                list: [],
                cancel: false,
                callback: false
            }
        },
        methods: {
            ok(value) {
                this.flag = false;
                this.callback && this.callback(value)
            }
        },
    }
</script>

