<template>
    <transition name="modal" v-if="flag" >
        <div class="modal"  :class="(type&&type!=='toast')&&'ok'">
            <div v-if="type==='confirm'" class="confirm">
                <div class="body">
                    <span>{{title}}</span>
                </div>
                <div class="btn">
                    <div @click="no">取消</div>
                    <span></span>
                    <div @click="ok">确定</div>
                </div>
            </div>
            <div v-if="type==='alert'" class="alert">
                <div class="body">
                    <span>{{title}}</span>
                </div>
                <div class="btn">
                    <div @click="ok">我知道了</div>
                </div>
            </div>
            <div v-if="type==='alertWhite'" class="alertWhite">
                <div class="body">
                    <span>{{title}}</span>
                </div>
                <div class="btn">
                    <div @click="ok">我知道了</div>
                </div>
            </div>
            <div v-if="type==='toast'" class="toast">
                <div>{{title}}</div>
            </div>
            <div v-if="type==='prompt'" class="prompt">
                <h1>{{title}}</h1>
                <div class="span">{{content}}</div>
                <div class="footer" @click="()=>{
                   this.flag=false;
                  this.callbackOk();
                }">
                    {{btnTitle||'知道了'}}
                </div>
            </div>
            <div v-if="type==='call'" class="confirm">
                <div class="body">
                    <span>{{title}}</span>
                </div>
                <div class="btn">
                    <div @click="no">取消</div>
                    <span></span>
                    <a :href="'tel:'+phone">确定</a>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
  export default {
    name: "modal",
    data() {
      return {
        input: '',
        flag: false,
        type: false,
        title: '',
        content: '',
        callbackOk: false,
        callbackNo: false,
        time: 3000,
        timeOut: null,
        phone: '020400400',
        btnTitle: '知道了'
      }
    },
    mounted() {

    },
    watch: {
      type() {
        if (this.type === 'toast') {
          this.timeOut = window.setTimeout(() => {
            this.flag = false;
            this.callbackOk && this.callbackOk();
            this.type = false;
          }, this.time)
        }
      }
    },
    methods: {
      ok() {
        this.flag = false;
        this.callbackOk && this.callbackOk();
      },
      no() {
        this.flag = false;
        this.callbackNo && this.callbackNo();
      },
    },
  }
</script>

