import ModalComponent from '../components/public/modal'// 引入先前写好的vue

const Modal = {};

// 注册Toast
Modal.install = function (Vue) {
  // 生成一个Vue的子类
  const ModalConstructor = Vue.extend(ModalComponent);
  // 生成一个该子类的实例
  const modal = new ModalConstructor();

  // 将这个实例挂载在我创建的div上
  // 并将此div加入全局挂载点内部
  modal.$mount(document.createElement('div'));
  document.body.appendChild(modal.$el);

  // 通过Vue的原型注册一个方法
  // 让所有实例共享这个方法
  const confirm = (title, callback, callbackTwo) => {
    modal.type = 'confirm';
    modal.title = title;
    modal.callbackOk = callback;
    modal.callbackNo = callbackTwo;
    modal.flag = false;
    modal.flag = true;
  };
  const alert = (title, callback, bool) => {
    bool ? modal.type = 'alertWhite' : modal.type = 'alert';
    modal.title = title;
    modal.callbackOk = callback;
    modal.flag = false;
    modal.flag = true;
  };
  // const toast = (title, callback) => {
  //   modal.type = 'toast';
  //   modal.title = title;
  //   modal.callbackOk = callback;
  //   modal.flag = false;
  //   modal.flag = true;
  // };
  const prompt = (title, content, callback, btnTitle) => {
    modal.type = 'prompt';
    modal.content = content;
    modal.title = title;
    if (btnTitle) {
      modal.btnTitle = btnTitle;
    }
    modal.callbackOk = callback;
    modal.flag = false;
    modal.flag = true;
  };
  const call = (title = '否拨打客服电话', phone) => {
    modal.type = 'call';
    modal.title = title;
    modal.phone = phone;
    modal.flag = false;
    modal.flag = true;
  };
  const hideModal=()=>{
    modal.flag=false;
  }
  window.M.confirm = Vue.prototype.$confirm = confirm;
  window.M.alert = Vue.prototype.$alert = alert;
  // window.M.toast = Vue.prototype.$toast = toast;
  window.M.prompt = Vue.prototype.$prompt = prompt;
  window.M.call = Vue.prototype.$call = call;
  window.M.hideModal = Vue.prototype.$hideModal = hideModal;
};
export default Modal;
