<template>
    <div class="collectShop">
        <nav-bar title="我的收藏" right="编辑" :right-callback="()=>{this.flag=!this.flag}"/>
        <div class="item" v-for="(item,index) in list"
             :key="index">
            <div class="editBox" v-show="flag">
                <i class="edit" :class="item.flag&&'active'" @click="item.flag=!item.flag"></i>
            </div>
            <router-link :to="{name:'storeIndex',query:{shop_id:item.shop_id}}" class="left">
                <img
                    src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561382307&di=a0ad6a21ea31489b5851e0b48b9455ff&imgtype=jpg&er=1&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F018a56569cb00f6ac725af23f38b1d.jpg"
                    alt="">
            </router-link>
            <router-link :to="{name:'storeIndex',query:{shop_id:item.shop_id}}" class="right">
                <h1>{{item.shop.name}}</h1>
                <p>{{item.shop.introduce}}</p>
            </router-link>
        </div>
        <load-more ref="loadMore" url="favorites" :params="params" @success="success"></load-more>
        <div class="deleteFooter" v-show="flag">
            <div class="left">
                <i class="edit" :class="allFLag&&'active'" @click="selectAll"></i>
                <span @click="selectAll">全选</span>
            </div>
            <div class="right" @click="del">
                删除
            </div>
        </div>
    </div>
</template>
<script>
  import LoadMore from "../../../components/public/loadMore";

  export default {
    name: "collectShop",
    components: {LoadMore},
    computed: {
      allFLag() {
        let flag = true;
        this.list.map(item => {
          if (item.flag === false) {
            flag = false;
          }
        });
        return flag;
      }
    },
    data() {
      return {
        flag: false,
        list: [],
        params: {
          shop_type: 10,
        }
      }
    },
    methods: {
      selectAll() {
        this.list = this.list.map(item => {
          return {
            ...item,
            flag: !this.allFLag
          }
        })
      },
      del() {
        const target_id = this.list.filter(item => {
          return item.flag;
        }).map(item => {
          return item.shop_id
        }).join(',');
        if(!target_id){
          return this.$toast('请选择店铺');
        }
        this.$http('delFavorites', {
          target_id: target_id
        }).then(()=>{
          this.list=[];
          this.$refs.loadMore.restart();
          })
      },
      success(data) {
        const newData = data.map(item => {
          return {
            ...item,
            flag: false
          }
        });
        this.list = this.list.concat(newData);
      }
    }
  }
</script>

