                </div>
                <div class="cart" @click="bugCar()">
                    <img src="../../../assets/img/shop/ic_shopping_cart.png" alt="">
                    <p>购物车</p>
                </div>
            </div>
            <div class="right">
                <div @click="addCart">加入购物车</div>
                <div @click="buy">立即购买</div>
            </div>
        </div>
