<script>
  import Carousel from "../../../components/home/carousel";
  import Remark from "../../../components/shop/remark";
  import {mapActions} from 'vuex';

  let sku = [];
  export default {
    name: "shopDetails",
    components: {Remark, Carousel},
    data() {
      return {
        data: {
          attrs: [],
          comment: {},
          shop: {}
        },
        sku: {},
      }
    },
    mounted() {
      this.getData();
      this.getGlobal({
        key: 'putMakeOrder',
        data: {}
      })
    },
    methods: {
      ...mapActions([
        'setShopData',
        'getGlobal'
      ]),
      getData() {
        this.$http('shopInfo', {}, {'product_id': this.$route.query.product_id}).then(res => {
          this.data = res.data;
        });
      },
      shopDetail(){
        this.callAppMethod('openWeb', {
          url:`/haoyin/storeIndex?shop_id=${this.data.shop.shop_id}`,
        }, 101)
      },
      bugCar(){
        this.callAppMethod('openWeb', {
          url:`/cart/index`,
        }, 101)
      },
      commentPage(){
        this.callAppMethod('openWeb', {
          url:`/haoyin/evaluate?url=shopEvaluateList&product_id=${this.$route.query.product_id}`,
        }, 101)
        // this.callAppMethod({
        //   method:'commentPage',
        //
        //   url:'shopEvaluateList',product_id:$route.query.product_id
        // })
        // :to="{name:'evaluate',query:{url:'shopEvaluateList',product_id:$route.query.product_id}}" -->
      },
      //处理第一个sku的函数，并且显示默认的sku
      calcSku(data) {
        if (data.data) {
          sku = data.data;
        } else {
          sku = this.data.sku_attributes.map(item => {
            return {
              ...item,
              ...{
                items: item.items.map((x, y) => {
                  if (y === 0) {
                    return {...x, ...{flag: true}}
                  }
                  return {...x, ...{flag: false}}
                })
              }
            }
          });
        }
        const name = sku.map(item => {
          return item.items.find(x => {
            return x.flag === true;
          }).value;
        });
        let params = {
          num: data.num
        };
        params.name = name.join(',');
        const title = name.join('');
        const skuInfo = this.data.skus.data.find(item => {
          return item.title === title;
        });
        this.sku = {
          ...skuInfo,
          ...params
        };
      },
      showSku() {
        if(!this.sku.price){
          this.calcSku({num: 1});
        }
        this.$skuShow(sku, (res) => {
          this.calcSku(res);
        });
      },
      buy() {
        if(!this.sku.price){
          this.calcSku({num: 1});
        }
        if (this.data.skus.data.length > 1) {
          return this.$skuShow(sku, (res) => {
            this.calcSku(res);
            this.pay();
          });
        }
        this.pay();
      },
      pay() {
        this.setShopData({
          key: 'orderCreateInfo', data: [
            {
              info: this.data,
              skus: [this.sku],
            }
          ]
        }).then(() => {
          this.callAppMethod('openWeb', {
            url:`/haoyin/putMakeOrder`,
          }, 101)
          //this.$navigation.push(this, {name: 'putMakeOrder'});
        })
      },
      addCart() {
        if(!this.sku.price){
          this.calcSku({num: 1});
        }
        if (this.data.skus.data.length > 1) {
          return this.$skuShow(sku, (res) => {
            this.calcSku(res);
            this.$http('addCart', {
              product_sku_id: this.sku.sku_id,
              amount: this.sku.num
            }).then(() => {
              this.$toast('成功添加购物车');
            })
          });
        }
        this.$http('addCart', {
          product_sku_id: this.sku.sku_id,
          amount: this.sku.num
        }).then(() => {
          this.$toast('成功添加购物车');
        })
      },
    }
  }
</script>
