<template>
    <div class="content_top evaluate">
        <nav-bar title="评价"></nav-bar>
        <div class="body">
            <remark v-for="(item,index) in list" :key="index" :data="item"></remark>
            <load-more :url="$route.query.url" :params="{shop_id:$route.query.shop_id}" :analysis="{product_id:$route.query.product_id}" @success="success"></load-more>
        </div>
    </div>
</template>

<script>
  import Remark from "../../../components/shop/remark";
  import LoadMore from "../../../components/public/loadMore";

  export default {
    name: "evaluate",
    components: {LoadMore, Remark},
    data() {
      return {
        list: []
      }
    },
    methods: {
      success(res) {
        this.list = this.list.concat(res);
      }
    }
  }
</script>

