<template>
    <div class="content_top login">
        <nav-bar title="登录" type="red"/>
        <div class="logo">
            <img src alt>
        </div>
        <div class="form">
            <form-input icon="user" type="text" v-model="params.account" @input="this.input" placeholder="手机号码/用户姓名"/>
            <form-input icon="pwd" type="password" v-model="params.password" @input="this.input" placeholder="登录密码"/>
        </div>
        <div class="info">
            <router-link class="getPwd" :to="{name:'getPwd'}">忘记密码</router-link>
        </div>
        <div class="loginBtn" :class="flag&&'active'" @click="submit">登录</div>
        <div class="moreBtn">
            <router-link class="left" :to="{name:'reg'}">
                <span>个人注册</span>
                <img src="../../assets/img/icon/ic_login_arrow_right_gray.png" alt>
            </router-link>
            <div class="center"></div>
            <router-link class="right" :to="{name:'unitReg'}">
                <span>单位注册</span>
                <img src="../../assets/img/icon/ic_login_arrow_right_gray.png" alt>
            </router-link>
        </div>
    </div>
</template>

<script>
  import {mapActions} from 'vuex';

  export default {
    name: "login",
    data() {
      return {
        flag: false,
        params: {
          account: "",
          password: ""
        }
      };
    },
    mounted() {
    },
    methods: {
      ...mapActions([
        'login'
      ]),
      input() {
        if (this.params.account && this.params.password) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      },
      submit() {
        if (!this.flag) {
          return false;
        }
        if (!this.params.account) {
          return this.$toast('账号不能为空');
        }
        if (!this.params.password) {
          return this.$toast('密码不能为空');
        }
        this.login(this.params).then(() => {
          this.$navigation.replace(this, {name: 'home'},true);
        });
      }
    }
  };
</script>

