import {SET_USER_INFO} from "../user/types";
import Http from "../../../tools/http";


const actions = {
  async login(context, params) {
    let data = await Http('login', params);
    await context.commit(SET_USER_INFO, {key: 'userInfo', data: data.data});
    await context.commit(SET_USER_INFO, {key: 'token', data: data.meta});
    return true;
  }
};

export default {
  actions,
};
