import {SET_USER_INFO} from './types';
import Http from "../../../tools/http";

const state = {
  userInfo: {},
  token: {},
  balance: 0,
};
const getters = {};
const actions = {
  async getUserInfo(context) {
    let res = await Http('userInfo');
    await context.commit(SET_USER_INFO, {key: 'userInfo', data: res.data})
  },
  async getBalance(context) {
    let res = await Http('account');
    await context.commit(SET_USER_INFO, {key: 'balance', data: res.data.money_asset})
  },
  async setUserInfo(context, params) {
    await context.commit(SET_USER_INFO, {key: 'userInfo', data: params});
  },
  async clearUser(context) {
    await context.commit(SET_USER_INFO, {key: 'userInfo', data: {}});
    await context.commit(SET_USER_INFO, {key: 'token', data: {}});
  }
};
const mutations = {
  [SET_USER_INFO](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
