<script>
  import {mapActions, mapState} from 'vuex';
  import {accAdd} from "../../../tools/functions";
  // import Qs from 'qs';

  export default {
    name: "putMakeOrder",
    data() {
      return {
        totalMoney: 0,
        data: [],
        pay: {
          target_type: 'store',
          payment_method: 'balance',//wechat alipay
          target_ids: [],
        }
      }
    },
    computed: {
      ...mapState({
        site: state => state.site.list.find(item => {
          return item.default === true
        }),
        orderCreateInfo: state => state.shop.orderCreateInfo,
        global: state => state.global,
        balance: state => state.user.balance
      })
    },
    async beforeMount() {
      this.getBalance();
      if (this.global.putMakeOrder.length) {
        this.data = this.global.putMakeOrder
      } else {
        if (this.$route.query.data) {
          const res = await this.$http('cartPay', {items: JSON.parse(this.$route.query.data)});
          this.totalMoney = res.data.totalMoney;
          const data = res.data.shops.map(item => {
            return {
              info: {
                shop: {
                  name: item.name,
                  shop_id: item.shop_id
                },
                ship_fee: item.ship_fee
              },
              skus: item.items.map(x => {
                return {
                  ...x,
                  ...{
                    name: x.title,
                    num: x.amount,
                    sku_id: x.product_sku_id,
                  }
                }
              }),
              receipt: {},
              totalMoney: item.total_amount || 0,
              totalNum: item.product_amount || 0,
            }
          });
          this.calcStart(data);
        } else {
          this.calcStart(this.orderCreateInfo);
        }
      }
    },
    methods: {
      ...mapActions([
        'getGlobal',
        'getBalance'
      ]),
      goLink(data) {
        this.$navigation.push(this, data)
      },
      go(index) {
        this.getGlobal({
          key: 'putMakeOrder',
          data: this.data
        });
        this.$navigation.push(this, {
          name: 'invoice',
          query: {name: 'putMakeOrder', index: index}
        })
      },
      calcStart(params) {
        let data = params;
        data = data.map(item => {
          return {
            ...item,
            totalNum: item.totalNum || item.skus.reduce((preValue, curValue) => {
              return (preValue.num ? preValue.num : 0) + curValue.num;
            }, 0),
            totalMoney: item.totalMoney || ((item.skus.reduce((preValue, curValue) => {
              return ((preValue.num ? preValue.num * preValue.price : 0) * 10000 + (curValue.num * curValue.price) * 10000);
            }, 0) + (item.info.ship_fee || 0) * 10000) / 10000),
            receipt: {}
          }
        });
        this.data = data;
        this.totalMoney = this.totalMoney || data.reduce((preValue, curValue) => {
          return accAdd((preValue.totalMoney ? preValue.totalMoney : 0), curValue.totalMoney);
        }, 0);
      },
      submit() {
        if (!this.site) {
          return this.$toast('请选择地址');
        }
        let params = {
          address_id: this.site.address_id || '',
          remove_cart: 0,
          items: [],
          shops: this.data.map(item => {
            let arr = {};
            arr['id'] = item.info.shop.shop_id;
            arr['remark'] = item.remark || '';
            return arr;
          }),
          receipt_data: this.data.map(item => {
            let arr = {};
            arr['shop_id'] = item.info.shop.shop_id;
            arr['receipt_id'] = item.receipt.receipt_id || '';
            return arr;
          }),
        };
        this.data.map(item => {
          item.skus.map(x => {
            params.items.push({
              sku_id: x.sku_id,
              amount: x.num
            })
          });
        });
        this.$http('storeCreateOrder', params).then(res => {

          this.pay.target_ids = res.data.map(item => {
            return item.id
          });

          this.$nextTick(() => {
            this.goPay();
          });
        })
      },
      goPay() {
        this.$http('pay', this.pay).then(() => {
          this.$navigation.push(this, {
            name: 'success', query: {
              name:'orderDetails',
              order_id:this.pay.target_ids[0]
            }
          });
          // this.$alert(res.data.info, () => {
          //   this.$navigation.replace(this, {name: 'user'});
          // })
        })
      }
    }
  }
</script>

