<template>
        <div class="content_top classify">
            <nav-bar title="分类"></nav-bar>
            <div class="body">
                <div class="classBox">
                    <div class="left">
                        <ul>
                            <li :class="oneIndex===index&&'active'" v-for="(item,index) in list" :key="index"
                                @click="select(index,1)"
                            >{{item.name}}
                            </li>
                        </ul>
                    </div>
                    <div class="right">
                        <ul v-if="list.length">
                            <li v-for="(item,index) in list[oneIndex].subs" :key="index" @click="select(index,2,item)">
                                <div class="icon">
                                    <img :src="item.image"
                                         alt="">
                                </div>
                                <div>{{item.name}}</div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
  export default {
    name: "classify",
    data() {
      return {
        list: [],
        oneIndex: 0,
        twoIndex: 0,
      }
    },
    created() {
      this.$http('storeClass').then(res => {
        this.list = res.data.filter(item => {
          return item.name !== '其他';
        });
        res.data.map((item, index) => {
          if (item.id === this.$route.query.id) {
            this.oneIndex = index;
          }
        });
        if (res.data[this.oneIndex].subs) {
          res.data[this.oneIndex].subs.map(((item, index) => {
            if (item.id === this.$route.query.index) {
              this.twoIndex = index
            }
          }))
        }
      })
    },
    methods: {
      select(index, type, item) {
        if (type === 1) {
          this.oneIndex = index;
          this.twoIndex = 0;
        } else {
          this.$navigation.push(this, {name: 'haoyinSearch', query: {category_id: item.id, name: item.name}});
        }
      }
    }
  }
</script>

