<template>
    <div class="item">
        <div class="top">
            <div class="left">订单编号：{{data.order_sn}}</div>
            <div class="right">{{data.order_state|orderTitle}}</div>
        </div>
        <router-link :to="{name:'details',query:{order_id:data.order_id}}" class="center">
            <div class="left">
                <img
                    src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1243183653,4064776920&fm=26&gp=0.jpg"
                    alt="">
            </div>
            <div class="right">
                <h1>{{data.shop_info.shop_name}}</h1>
                <p>服务类型：{{data.cate_info.cate_name}}</p>
                <p>送货时间：{{data.deliver_time}}</p>
            </div>
        </router-link>
        <div class="bottom">
            <div class="left">
                <span v-if="data.order_state!==10">¥ {{data.total_amount}}</span>
            </div>
            <div class="right">
                <div v-if="data.order_state===10" @click="setorder(2)">取消订单</div>
                <div v-if="data.order_state===50" @click="setorder(990)">删除订单</div>
                <div v-if="data.order_state===20" @click="goPay" class="active">支付订单</div>
                <div v-if="data.order_state===30" @click="setorder(40)" class="active">待收货</div>
                <router-link v-if="data.order_state===40||data.order_state===50"
                             :to="{name:'orderComplain',query:{order_id:data.order_id,type:10}}">投诉订单
                </router-link>
                <router-link v-if="data.order_state===40" :to="{name:'orderEvaluate',query:{order_id:data.order_id}}" class="active">去评价</router-link>
            </div>
        </div>
    </div>
</template>

<script>
  //订单状态；10 默认 待确认；20 已确认，待付款；30 已付款，待收货；40 已收货，待评价；50 已完成
  import {mapActions, mapState} from "vuex";

  export default {
    name: "serviceOrderItem",
    props: ['data'],
    filters: {
      orderTitle(value) {
        switch (value) {
          case 1:
            return '已关闭';
          case 2:
            return '已取消';
          case 3:
            return '已删除';
          case 10:
            return '待商家确认';
          case 20:
            return '待付款';
          case 30:
            return '待收货';
          case 40:
            return '待评价';
          case 50:
            return '已评价';
        }
      }
    },
    computed:{
      ...mapState({
        balance:state=>state.user.balance
      })
    },
    beforeMount(){
      this.getBalance();
    },
    methods: {
      ...mapActions([
        'getBalance'
      ]),
      goPay() {
        let params = {
          target_type: 'store_service',
          target_id: this.data.order_id,
          payment_method: '',
        };
        this.$payShow(this.data.total_amount,this.balance, (payment_method) => {
          params.payment_method = payment_method;
          this.$http('pay', params).then(() => {
            this.$navigation.push(this, {
              name: 'success', query: {
                name:'details',
                order_id:params.target_id
              }
            });
            // this.$emit('restart');
          })
        });
      },
      setorder(status) {
        //40确认收货；2订单取消；990删除订单
        this.$http('serviceOrderStatus', {status: status}, {order_id: this.data.order_id}).then(() => {
          this.$emit('restart');
        })
      }
    }
  }
</script>

