const now = new Date();
const nowYear = now.getFullYear();
const nowMonth = now.getMonth() + 1;
const nowDay = now.getDay();

export function formatYear(nowYear) {
  let arr = [];
  for (let i = nowYear; i <= nowYear + 5; i++) {
    arr.push({
      id: i + '',
      value: i + '年'
    });
  }
  return arr;
}

export function formatYearBirthday(nowYear) {
  let arr = [];
  for (let i = nowYear - 100; i <= nowYear; i++) {
    arr.push({
      id: i + '',
      value: i + '年'
    });
  }
  return arr;
}

export function formatMonth(year, type) {
  let arr = [];
  if (type === 0 && year == nowYear) {
    for (let i = 1; i <= nowMonth; i++) {
      arr.push({
        id: i + '',
        value: i + '月'
      });
    }
    return arr;
  }
  if (type === 1 && year == nowYear) {
    for (let i = 12; i >= nowMonth; i--) {
      arr.push({
        id: i + '',
        value: i + '月'
      });
    }
    return arr.reverse();
  }
  for (let i = 1; i <= 12; i++) {
    arr.push({
      id: i + '',
      value: i + '月'
    });
  }
  return arr;
}

export function formatDate(count, year, month, type) {
  if (type === 0 && year == nowYear && nowMonth == month) {
    let arr = [];
    for (let i = 1; i <= count; i++) {
      if (i <= nowDay) {
        arr.push({
          id: i + '',
          value: i + '日'
        });
      }
    }
    return arr;
  }
  if (type === 1 && year == nowYear && nowMonth == month) {
    let arr = [];
    for (let i = 1; i <= count; i++) {
      if (i >= nowDay) {
        arr.push({
          id: i + '',
          value: i + '日'
        });
      }
    }
    return arr;
  }
  let arr = [];
  for (let i = 1; i <= count; i++) {
    arr.push({
      id: i + '',
      value: i + '日'
    });
  }
  return arr;
}

export function yearData(callback) {
  callback(formatYear(nowYear))
}

export function yearDataBirthday(callback) {
  callback(formatYearBirthday(nowYear))
}

export function monthData1(year, callback) {
  callback(formatMonth(year, 0));
}

export function monthData(year, callback) {
  callback(formatMonth(year, 1));
}


export function dateData1(year, month, callback) {

  if (/^(1|3|5|7|8|10|12)$/.test(month)) {
    callback(formatDate(31, year, month, 0));
  } else if (/^(4|6|9|11)$/.test(month)) {
    callback(formatDate(30, year, month, 0));
  } else if (/^2$/.test(month)) {
    if (year % 4 === 0 && year % 100 !== 0 || year % 400 === 0) {
      callback(formatDate(29, year, month, 0));
    } else {
      callback(formatDate(28, year, month, 0));
    }
  } else {
    throw new Error('month is illegal');
  }
}

export function dateData(year, month, callback) {

  if (/^(1|3|5|7|8|10|12)$/.test(month)) {
    callback(formatDate(31, year, month, 1));
  } else if (/^(4|6|9|11)$/.test(month)) {
    callback(formatDate(30, year, month, 1));
  } else if (/^2$/.test(month)) {
    if (year % 4 === 0 && year % 100 !== 0 || year % 400 === 0) {
      callback(formatDate(29, year, month, 1));
    } else {
      callback(formatDate(28, year, month, 1));
    }
  } else {
    throw new Error('month is illegal');
  }
}

export function hourData(one, two, three, callback) {
  let hours = [];
  for (let i = 0, len = 24; i < len; i++) {
    hours.push({
      id: i,
      value: i + '时'
    });
  }
  callback(hours);
}

export function minuteData(one, two, three, four, callback) {
  let minutes = [];
  for (let i = 0, len = 60; i < len; i++) {
    minutes.push({
      id: i,
      value: i + '分'
    });
  }
  callback(minutes);
}

// 两个浮点数求和
export function accAdd(num1, num2) {
  let r1, r2, m;
  try {
    r1 = num1.toString().split('.')[1].length;
  } catch (e) {
    r1 = 0;
  }
  try {
    r2 = num2.toString().split(".")[1].length;
  } catch (e) {
    r2 = 0;
  }
  m = Math.pow(10, Math.max(r1, r2));
  // return (num1*m+num2*m)/m;
  return Math.round(num1 * m + num2 * m) / m;
}

// 两个浮点数相减
export function accSub(num1, num2) {
  let r1, r2, m;
  try {
    r1 = num1.toString().split('.')[1].length;
  } catch (e) {
    r1 = 0;
  }
  try {
    r2 = num2.toString().split(".")[1].length;
  } catch (e) {
    r2 = 0;
  }
  m = Math.pow(10, Math.max(r1, r2));
  let n = (r1 >= r2) ? r1 : r2;
  return (Math.round(num1 * m - num2 * m) / m).toFixed(n);
}

// 两数相除
export function accDiv(num1, num2) {
  let t1, t2, r1, r2;
  try {
    t1 = num1.toString().split('.')[1].length;
  } catch (e) {
    t1 = 0;
  }
  try {
    t2 = num2.toString().split(".")[1].length;
  } catch (e) {
    t2 = 0;
  }
  r1 = Number(num1.toString().replace(".", ""));
  r2 = Number(num2.toString().replace(".", ""));
  return (r1 / r2) * Math.pow(10, t2 - t1);
}

export function accMul(num1, num2) {
  let m = 0, s1 = num1.toString(), s2 = num2.toString();
  try {
    m += s1.split(".")[1].length
  } catch (e) {
  }
  try {
    m += s2.split(".")[1].length
  } catch (e) {
  }
  return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m);
}

export const formatNum = function (f, digit) {
  let m = Math.pow(10, digit);
  return parseInt(f * m, 10) / m;
};
