<template>
    <div class="content_top invoice">
        <nav-bar title="发票信息"
                 :right="`<img class='rightImg' src='${require('../../../assets/img/shop/ic_invoice_help.png')}'  />`"
                 :right-callback="()=>{
                 this.$navigation.push(this,{name:'invoiceIssue'})
                 }"
        ></nav-bar>
        <div class="body">
            <div class="nav">
                <h1>发票类型</h1>
                <div class="navContent">
                    <div :class="params.type===10&&'active'" @click="()=>{tab(10)}">电子普通发票</div>
                    <div :class="params.type===20&&'active'" @click="()=>{tab(20)}">纸质普通发票</div>
                </div>
                <div class="footer">
                    电子发票即电子增值税发票，是税局认可的有效凭证，其法律效力、基本用途及使用规定同纸质发票
                </div>
            </div>
            <div class="info">
                <div class="item" @click="fpType">
                    <div class="left">
                        <span>*</span>
                        <span>发票抬头</span>
                    </div>
                    <div class="right">
                        {{params.rise_type===10?'个人':'企业'}}
                    </div>
                </div>
                <div class="item">
                    <div class="left">
                        <span>*</span>
                        <span>发票内容</span>
                    </div>
                    <div class="right">
                        <input type="text" placeholder="请输入发票内容" v-model="params.content">
                    </div>
                </div>
            </div>
            <div class="info" v-if="params.type===10">
                <div class="item">
                    <div class="left">
                        <span>*</span>
                        <span>收票人邮箱</span>
                    </div>
                    <div class="right">
                        <input type="text" placeholder="请输入邮箱地址" v-model="params.address_email">
                    </div>
                </div>
            </div>
            <div class="info" v-if="params.type===20">
                <div class="item">
                    <div class="left">
                        <span>*</span>
                        <span>邮寄地址</span>
                    </div>
                    <div class="right">
                        <input type="text" placeholder="请输入地址" v-model="params.address">
                    </div>
                </div>
            </div>
            <div class="info" v-if="params.type===20">
                <div class="item">
                    <div class="left">
                        <span>*</span>
                        <span>联系人</span>
                    </div>
                    <div class="right">
                        <input type="text" placeholder="请输入联系人" v-model="params.receive_name">
                    </div>
                </div>
            </div>
            <div class="btn active" @click="submit">
                保存
            </div>
            <div class="message">
                <h1>
                    <img src="../../../assets/img/shop/ic_invoice_remind.png" alt="">
                    <span>发票须知</span>
                </h1>
                <ul>
                    <li>1.开票金额为用户实际支付金额。</li>
                    <li>2.电子发票可在确认收货后，在“订单详情“查看</li>
                    <li>3.未随箱寄出的纸质发票会在发货后7工作日单独寄出</li>
                    <li>4.单笔订单只支持开具一种发票类型，如需增值税专用发票请联系客服处理</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapActions, mapState} from 'vuex';

  export default {
    name: "invoice",
    data() {
      return {
        params: {
          type: 10,
          rise_type: 10,
          content: '',
          address_email: '',
          address: '',
          receive_name: '',
        }
      }
    },
    mounted() {
    },
    computed: {
      ...mapState({
        global: state => state.global
      })
    },
    methods: {
      ...mapActions([
        'getGlobal'
      ]),
      tab(index) {
        this.params.type = index;
      },
      fpType() {
        this.$selectShow({
          data: [
            {title: '个人', value: '10'},
            {title: '企业', value: '20'},
          ], cancel: true, callback: value => this.params.rise_type = value,
        });
      },
      submit() {
        let params = {};
        if (this.params.type === 10) {
          params = {
            type: 10,
            rise_type: this.params.rise_type,
            content: this.params.content,
            address_email: this.params.address_email,
          };
        } else {
          params = {
            type: 20,
            rise_type: this.params.rise_type,
            content: this.params.content,
            address: this.params.address,
            receive_name: this.params.receive_name,
          };
        }
        this.$http('addReceipt', params).then(res => {
          const name = this.$route.query.name;
          if (name === 'putMakeOrder') {
            const index = this.$route.query.index;
            this.getGlobal({
              key: name,
              data: this.global[name].map((item, y) => {
                if (index === y) {
                  return {
                    ...item,
                    receipt: res.data
                  }
                } else {
                  return item;
                }
              }),
            });
          } else {
            this.getGlobal({
              key: name,
              data: {
                ...this.global[name],
                receipt_type: this.params.type,
                receipt_id: res.data.receipt_id
              }
            });
          }
          this.$navigation.back(this);
        })
      }
    }
  }
</script>

