<template>
    <div class="content cart">
        <nav-bar title="购物车" :right="type?'编辑':'完成'" :right-callback="edit"/>
        <div class="body">
            <div class="list">
                <div class="item" v-for="(item,index) in list" :key="Math.random()+index">
                    <h1 @click="()=>goLink({name:'storeIndex',query:{shop_id:item.shop_id||''}})" >
                        <i class="edit" :class="item.flag&&'active'" @click.stop="select(item)"></i>
                        <img class="icon" src="../../assets/img/shop/ic_shop_small.png" alt="">
                        <span>
                            {{item.name}}
                        </span>
                        <img class="rightIcon" src="../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                    </h1>
                    <div class="itemTwo" v-for="(x,y) in item.items" :key="Math.random()+y">
                        <div class="info"  @click="()=>goLink({name:'shopDetails',query:{product_id:x.product_id}})">
                            <i class="edit" :class="x.flag&&'active'" @click.stop="select(item,x)"></i>
                            <div class="head">
                                <img :src="x.image" alt="">
                            </div>
                            <div class="message">
                                <h1>{{x.title}}</h1>
                                <div class="specification" @click.stop="()=>showSku(x.skus,x.item_id,x.product_sku_id)">
                                    <div>{{x|skuTitle}}</div>
                                    <img src="../../assets/img/cart/ic_arrow_down_gray.png" alt="">
                                </div>
                            </div>
                        </div>
                        <div class="footer">
                            <div class="left">
                                <span>￥</span>
                                <span>{{x.price}}</span>
                            </div>
                            <div class="right">
                                <span :class="x.amount>1&&'active'" @click="updateCart(x,false)">-</span>
                                <div>{{x.amount}}</div>
                                <span class="active" @click="updateCart(x,true)">+</span>
                            </div>
                        </div>
                    </div>
                </div>
                <h1 v-show="noList.length">已失效商品</h1>
                <div class="item" v-for="(item,index) in noList" :key="Math.random()+index">
                    <h1 @click="()=>goLink({name:'storeIndex',query:{shop_id:item.shop_id||''}})" >
                        <i class="edit" :class="item.flag&&'active'" @click.stop="select(item)"></i>
                        <img class="icon" src="../../assets/img/shop/ic_shop_small.png" alt="">
                        <span>
                            {{item.name}}
                        </span>
                        <img class="rightIcon" src="../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                    </h1>
                    <div class="itemTwo" v-for="(x,y) in item.items" :key="Math.random()+y">
                        <div class="info"  @click="()=>goLink({name:'shopDetails',query:{product_id:x.product_id}})">
                            <i class="edit" :class="x.flag&&'active'" @click.stop="select(item,x)"></i>
                            <div class="head">
                                <img :src="x.image" alt="">
                            </div>
                            <div class="message">
                                <h1>{{x.title}}</h1>
                                <div class="specification" @click="$skuShow">
                                    <div>{{x|skuTitle }}</div>
                                    <img src="../../assets/img/cart/ic_arrow_down_gray.png" alt="">
                                </div>
                            </div>
                        </div>
                        <div class="footer">
                            <div class="left">
                                <span>￥</span>
                                <span>{{x.price}}</span>
                            </div>
                            <div class="right">
                                <span>-</span>
                                <div>{{x.amount}}</div>
                                <span>+</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="left">
                <div class="left" @click="selectAll">
                    <i class="edit" :class="allFlag&&'active'"></i>
                    <span>全选</span>
                </div>
                <div class="right" v-if="type">
                    <span class="no">不含运费</span>
                    <span class="he">合计：</span>
                    <span class="price">￥{{total}}</span>
                </div>
            </div>
            <div v-if="type" class="right" @click="goPay">
                付款
            </div>
            <div v-else class="right delete" @click="delCart">
                删除
            </div>
        </div>
        <nav-foot/>
    </div>
</template>
<script>
  import {mapActions} from 'vuex';
  import {accAdd, accMul} from '../../tools/functions'

  export default {
    name: "cartIndex",
    data() {
      return {
        allFlag: false,
        type: true,//true 付款,false 删除
        list: [],
        noList: [],
      }
    },
    filters: {
      skuTitle(data) {
        return data.skus.find(item => {
          return item.id === data.product_sku_id;
        }).sub_title;
      }
    },
    computed: {
      total: function () {
        let total = 0;
        this.list.concat(this.noList).map(item => {
          item.items.map(x => {
            // total += x.amount * x.price;
            if (x.flag) {
              total = accAdd(total, accMul(x.amount, x.price));
            }
          })
        });
        return total;
      }
    },
    beforeMount() {
      this.getCart();
      this.setReceipt([]);
    },
    methods: {
      ...mapActions(['setReceipt']),
      goLink(data) {
        this.$navigation.push(this, data)
      },
      getCart() {
        this.$http('cart').then(res => {
          this.list = res.data.shops.enable.map(item => {
            return {
              ...item,
              ...{
                disable: false,
              },
              ...{
                flag: false,
                items: item.items.map(item => {
                  return {
                    ...item,
                    ...{
                      disable: false,
                    },
                    ...{
                      flag: false
                    }
                  }
                })
              }
            }
          });
          this.noList = res.data.shops.disable.map(item => {
            return {
              ...item,
              ...{
                disable: true,
              },
              ...{
                flag: false,
                items: item.items.map(item => {
                  return {
                    ...item,
                    ...{
                      disable: true,
                    },
                    ...{
                      flag: false
                    }
                  }
                })
              }
            }
          });
        })
      },
      showSku(data, item_id, sku_id) {
        let params = [];
        let calcItem = [];
        let calcName = [];
        data.map(item => {
          item.attributes.map(x => {
            calcName.push(x.name);
            calcItem.push({
              name: x.name,
              items: {
                flag: item.id === sku_id,
                item_id: item_id,
                value: x.value,
              }
            })
          })
        });
        calcName = new Set(calcName);
        calcName.forEach(item => {
          let obj = {};
          params.push({
            name: item,
            items: calcItem.filter(x => {
              return x.name === item;
            }).map(y => {
              return {
                ...y.items,
              }
            }).reduce((cur, next) => {
              if (next.flag) {
                if (obj[next.value]) {
                  cur[obj[next.value]].flag = true;
                } else {
                  cur.push(next);
                  obj[next.value] = (cur.length - 1).toString();
                }
              } else {
                if (!obj[next.value]) {
                  cur.push(next);
                  obj[next.value] = (cur.length - 1).toString();
                }
              }
              return cur;
            }, [])
          });
        });
        this.$skuShow(params, res => {
          const name = res.data.map(item => {
            return item.items.find(x => {
              return x.flag === true;
            }).value;
          });
          const skuInfo = data.find(item => {
            return item.title === name.join('');
          });
          const cParmas = {
            item_id: item_id,
            product_sku_id: skuInfo.id,
            amount: res.num
          };
          this.$http('changeCart', cParmas).then(() => {
            this.getCart()
          });
        });
      },
      select(item, item2) {
        if (!item2) {
          if (this.type && item.disable) {
            return false;
          }
          item.flag = !item.flag;
          item.items = item.items.map(x => {
            return {
              ...x,
              ...{
                flag: item.flag
              }
            }
          })
        } else {
          if (this.type && item2.disable) {
            return false;
          }
          item2.flag = !item2.flag;
          let flag = true;
          item.items.map(x => {
            if (!x.flag) {
              flag = false;
            }
          });
          item.flag = flag;
        }
        //全选状态更新
        let allFlag = true;
        this.list.concat(this.noList).map(item => {
          item.items.map(x => {
            if (x.flag === false) {
              allFlag = false
            }
          })
        })
        this.allFlag = allFlag;
      },
      selectAll() {
        const flag = this.allFlag = !this.allFlag;
        this.list = this.list.map(item => {
          return {
            ...item,
            ...{
              flag: flag,
              items: item.items.map(item => {
                return {
                  ...item,
                  ...{
                    flag: flag
                  }
                }
              })
            }
          }
        });
        if (!this.type) {
          this.noList = this.noList.map(item => {
            return {
              ...item,
              ...{
                flag: flag,
                items: item.items.map(item => {
                  return {
                    ...item,
                    ...{
                      flag: flag
                    }
                  }
                })
              }
            }
          });
        }
      },
      edit() {
        this.type = !this.type;
      },
      updateCart(item, flag) {
        let num;
        if (flag) {
          num = ++item.amount;
        } else {
          num = item.amount > 1 ? --item.amount : 1;
        }
        // this.total=
        this.$http('upDateCart', {
          product_sku_id: item.product_sku_id,
          amount: num
        })
      },
      delCart() {
        let product_sku_ids = [];
        this.list.map(item => {
          item.items.map(x => {
            if (x.flag) {
              product_sku_ids.push(x.product_sku_id)
            }
          })
        });
        this.noList.map(item => {
          item.items.map(x => {
            if (x.flag) {
              product_sku_ids.push(x.product_sku_id)
            }
          })
        });
        if (!product_sku_ids.length) {
          return this.$toast('请选择商品')
        }
        this.$http('delCart', {product_sku_ids: product_sku_ids.join(',')}).then(() => {
          this.getCart()
        })
      },
      goPay() {
        //items[][sku_id] items[][amount]
        let items = [];
        const list = this.list.concat(this.noList);
        list.map(item => {
          item.items.map(x => {
            if (x.flag === true) {
              items.push(
                {
                  sku_id: x.product_sku_id,
                  amount: x.amount,
                }
              );
            }
          })
        });
        if (!items.length) {
          this.$alert('请选择商品');
          return false;
        }
        this.$navigation.push(
          this,
          {
            name: 'putMakeOrder',
            query: {
              data: JSON.stringify(items),
              title:'订单支付'
            }
          }
        )
      }
    }
  }
</script>

