<template>
    <div class="content_top opinion">
        <nav-bar title="意见反馈"></nav-bar>
        <div class="body">
            <div class="input">
                <div class="text">
                    <textarea v-model="params.content" @input="input" placeholder="请填写你的意见反馈"></textarea>
                </div>
                <div class="footer">
                    <span>{{params.content.length}}</span>/200
                </div>
            </div>
            <div class="btn" :class="flag&&'active'" @click="submit">提交</div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "opinion",
    data() {
      return {
        flag: false,
        params: {
          content: ''
        }
      }
    },
    methods: {
      submit() {
        if (!this.flag) {
          return false;
        }
        if (!this.params.content) {
          return this.$toast('请填写你的意见反馈');
        }
        this.$http('feedBacks', this.params).then(() => {
          this.$toast('已成功提交您的意见反馈', () => {
            this.$navigation.back(this);
          })
        })
      },
      input() {
        if (this.params.content.length > 0) {
          this.flag = true;
        }
        if (this.params.content.length > 200) {
          this.params.content = this.params.content.substr(0, 200)
        }
      }
    }
  }
</script>

