import {SET_SHOP_DATA} from './types';
import Http from "../../../tools/http";

const state = {
  orderCreateInfo: [],//创建订单保存的信息
  menu: [],
};
const getters = {};
const actions = {
  async setShopData(context, params) {
    await context.commit(SET_SHOP_DATA, params)
  },
  async getMenu(context) {
    const data = await Http('menu');
    await context.commit(SET_SHOP_DATA, {
      key: 'menu',
      data: data.data
    })
  }
};
const mutations = {
  [SET_SHOP_DATA](state, params) {
    state[params.key] = params.data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations
}
