Vue.prototype.callAppMethod = function(methodName, data, requestCode) {
  return new Promise((resolve, reject) => {
    var u = navigator.userAgent
    if (u.indexOf('native-app') != -1) {
      const json = {
        method: methodName,
        data: data
      }
      if (requestCode !== undefined) {
        json.requestCode = requestCode
      }
      if (window.webkit !== undefined) {
        if (window.webkit.messageHandlers !== undefined) {
          window.webkit.messageHandlers.app.postMessage(JSON.stringify(json))
        }
      }
      if (window.app !== undefined) {
        window.app.postMessage(JSON.stringify(json))
      }
      reject()
    } else {
      resolve()
    }
  })
}
//store
import store from './store/index';

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');

Vue.prototype.receiveAppMessage = function(that, json) {
  const post = JSON.parse(json)
  const name = post.method
  const requestCode = post.requestCode || 0
  const data = post.data
  switch (name) {
    case 'onResult':
      that.onResult(data, requestCode)
      break
  }
}
