<template>
    <div class="content_top userInfo">
        <nav-bar title="我的资料"/>
        <div class="body">
            <div class="input">
                <div class="left">
                    头像
                </div>
                <div class="center">
                    <img :src="userInfo.avatar||require('../../../assets/img/user/ic_mine_head_portrait.png')" alt="">
                    <input type="file" accept="image/png,image/jpg,image/jpeg" @change="uploadImg($event)"/>
                </div>
            </div>
            <router-link class="input" :to="{name:'infoInput'}">
                <div class="left">
                    昵称
                </div>
                <div class="center">
                    {{userInfo.nickname}}
                </div>
                <div class="right">
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </div>
            </router-link>
            <div class="input" @click="selectSex">
                <div class="left">
                    性别
                </div>
                <div v-if="!userInfo.gender" class="center">
                    请选择性别
                </div>
                <div v-else-if="userInfo.gender===1" class="center">
                    男
                </div>
                <div v-else class="center">
                    女
                </div>
                <div class="right">
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </div>
            </div>
            <div class="input" @click="timeShow">
                <div class="left">
                    生日
                </div>
                <div class="center">
                    {{userInfo.birthday}}
                </div>
                <div class="right">
                    <img src="../../../assets/img/icon/ic_login_arrow_right_gray.png" alt="">
                </div>
            </div>
        </div>
        <div class="body2" v-show="userInfo.is_unit">
            <div class="input">
                <div class="left">
                    所属单位
                </div>
                <div class="center">
                    {{userInfo.unit_name}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';
  import IosSelect from 'iosselect';
  import * as Fun from '../../../tools/functions';

  export default {
    name: "info",
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo
      })
    },
    mounted() {
    },
    methods: {
      ...mapActions(['setUserInfo']),
      async uploadImg($event) {
        let formData = new FormData();
        const self = $event.target;
        formData.append('file', self.files[0]);
        this.$http('upload', formData).then(res => {
          this.$http('setUserInfo', {
            avatar: res.data.url,
          }).then(re => {
            this.setUserInfo(re.data);
          })
        });
      },
      selectSex() {
        this.$selectShow({
          data: [
            {title: '男', value: 1},
            {title: '女', value: 2},
          ], cancel: true, callback: value => {
            this.$http('setUserInfo', {
              gender: value,
            }).then(re => {
              this.setUserInfo(re.data);
            })
          },
        });
      },
      timeShow() {
        let nowTime = new Date();
        new IosSelect(3,
          [Fun.yearDataBirthday, Fun.monthData1, Fun.dateData1],
          {
            container: '.container',
            title: '时间选择',
            itemHeight: 35,
            itemShowCount: 9,
            oneLevelId: nowTime.getFullYear(),
            twoLevelId: nowTime.getMonth() + 1,
            threeLevelId: nowTime.getDate(),
            callback: (selectOneObj, selectTwoObj, selectThreeObj) => {
              const time = parseInt(selectOneObj.value) + '-' + parseInt(selectTwoObj.value) + '-' + parseInt(selectThreeObj.value);
              this.$http('setUserInfo', {
                birthday: time,
              }).then(re => {
                this.setUserInfo(re.data);
              })
            }
          });
      },
    }
  }
</script>

