    .userInfo {
        width: 100%;
        height: 100%;
        background: #F5F5F5;

        .body, .body2 {
            .input {
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 91px;
                border-top: 0;
                border-bottom: 1px solid transparent;
                border-image: svg(borderRow param(--color #e0e0e0)) 2 2 stretch;
                background: #fff;

                &:last-child {
                    border-bottom: none;
                    border-image: none;;
                }

                .left {
                    font-size: 29px;
                    color: #000;
                    margin-left: 28px;
                }

                .center {
                    flex: 1;
                    text-align: right;
                    margin-right: 21px;
                    font-size: 29px;
                    color: #666;
                    height: 100%;
                    line-height: 91px;
                    position: relative;

                    input[type=file] {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                    }

                    img {
                        width: 75px;
                        height: 75px;
                        border-radius: 75px;
                        margin-right: 10px;
                        vertical-align: middle;
                    }
                }

                .right {
                    line-height: 31px;
                    margin-right: 31px;

                    img {
                        width: 13px;
                    }
                }
            }

            &.body2 {
                margin-top: 18px;
            }

            .input {
                .center {
                    margin-right: 31px;
                }
            }
        }
    }
</style>
