<template>
    <router-view></router-view>
</template>

<script>
    import {mapActions,mapState} from 'vuex';
  export default {
    name: "homeRoot",
    computed:{
      ...mapState({
        token: state => state.user.token
      })
    },
    beforeMount(){
      if (this.token.access_token) {
        this.getSiteList();
      }
    },
    methods:{
      ...mapActions([
        'getSiteList'
      ])
    }
  }
</script>
