<template>
    <div id="appContent">
        <router-view></router-view>
    </div>
</template>
<script>
  import {mapState, mapActions} from 'vuex';
  import config from './conf/conf';
  import clearModal from './tools/clearModal';

  export default {
    name: "app",
    data() {
      return {
        mapAk: config.mapAk,
        transitionName: "",
        center: {
          lang: 0,
          lat: 0,
        },
        zoom: 3,
      };
    },
    computed: {
      ...mapState({
        token: state => state.user.token
      })
    },
    watch: {
      $route(to, form) {
        clearModal(this);
        const animate = to.params.animate || false;
        let name = to.name;
        name = name === 'homeIndex' || name === 'haoyinIndex' || name === 'cartIndex' || name === 'userIndex' || false;
        let formName = form.name;
        formName = formName === 'homeIndex' || formName === 'haoyinIndex' || formName === 'cartIndex' || formName === 'userIndex' || false;
        if (name && formName) {
          this.transitionName = 'fade';
        } else if (animate === 1) {
          this.transitionName = 'slide-left';
        } else {
          this.transitionName = 'slide-right';
        }
      }
    },
    beforeMount() {
      this.$locationPoint(res => {
        if (res) {
          if (res.code === 200) {
            this.setMap(res.data)
          }
        }
      });
      this.getConfig();
      this.getServiceTypeList();
      if (this.token.access_token) {
        this.getSiteList();
      }
    },
    methods: {
      ...mapActions(['getConfig', 'getServiceTypeList', 'getSiteList', 'setMap']),
    },
  };
</script>

<style scoped lang="less">
    .slide-right-enter-active,
    .slide-right-leave-active,
    .slide-left-enter-active,
    .slide-left-leave-active {
        will-change: transform;
        transition: all 300ms;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: 300ms ease-out;
    }

    .fade-enter {
        opacity: 0;
        /*transform: scale(1.2);*/
    }

    .fade-leave-to {
        opacity: 0;
        /*transform: scale(0.8);*/
    }

    .slide-right-enter {
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }

    .slide-right-leave-active {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }

    .slide-left-enter {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }

    .slide-left-leave-active {
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }

    #appContent {
        /*width: 100%;*/
        /*height: 100%;*/
    }
</style>
