<template>
    <div class="item" @click="go">
        <div class="left">
            <img v-if="type==1||!type" :src="data.logo" alt="">
            <img v-else-if="type==2" :src="data.image" alt="">
        </div>
        <div class="right" v-if="!type||type==='1'">
            <h1 class="title">{{data.name}}</h1>
            <p>{{data.address}}</p>
            <div class="bottom">
                <div class="start">
                    <star :num="parseFloat(data.star)"></star>
                </div>
                <div class="info">{{data.distance}}米</div>
            </div>
        </div>
        <div class="right2" v-else-if="type==='2'">
            <h2>{{data.title}}</h2>
            <div class="bottom">
                <div class="money">
                    <span>¥</span>
                    <span>{{data.price}}</span>
                </div>
                <div class="info">
                    <span>销量：</span>
                    <span>{{data.sold_count}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import Star from "../public/star";

  export default {
    name: "item",
    components: {Star},
    props: ['type', 'data', 'serviceType','point'],
    methods: {
      go() {
        if (!this.type || this.type === '1') {
          this.$navigation.push(this, {
            name: 'particulars',
            query: {shop_id: this.data.shop_id, type: this.serviceType,point:this.point}
          });
        } else {
          this.$navigation.push(this, {name: 'shopDetails', query: {product_id: this.data.goods_id}});
        }
      }
    }
  }
</script>

