<template>
    <div class="baiduMap">
        <baidu-map class="map" center="河南省郑州市" @ready="ready"/>
    </div>
</template>

<script>
  let info = {
    lng: 113,
    lat: 34,
  };
  export default {
    name: "siteMap",
    data() {
      return {
        flag: false,
        search: '',
        callback: () => {
        },
        queue: [],//事件队列
        BMap: null,
        map: null,
        type: 'location',//location,address
      }
    }
    ,
    watch: {
      flag() {
        if (this.flag === true) {
          if (this.type === 'location') {
            if (this.BMap === null || this.map === null) {
              this.queue.push({
                fun: this.location,
                callback: () => {
                  this.flag = false;
                  this.callback()
                }
              })
            } else {
              this.location(this.callback);//定位
            }
          } else {
            if (this.BMap === null || this.map === null) {
              this.queue.push({
                fun: this.address,
                callback: () => {
                  this.flag = false;
                  this.callback()
                }
              })
            } else {
              this.address(this.callback);
            }
          }
        }
      }
    }
    ,
    methods: {
      ready({BMap, map}) {
        this.BMap = BMap;
        this.map = map;
        if (this.queue.length) {
          this.queue.map(item => {
            item.fun(item.callback);
          })
        }
      }
      ,
      location(callback) {
        const _this = this;
        const geolocation = new _this.BMap.Geolocation();
        geolocation.getCurrentPosition(function (r) {
          if (this.getStatus() === 0) {
            const map = {lng: r.point.lng, lat: r.point.lat,site:`${r.address.city}`};
            info = {lng: r.point.lng, lat: r.point.lat};
            this.flag = false;
            callback && callback({
              code: 200,
              data: map
            });
          } else {
            this.flag = false;
            callback && callback({
              code: 404,
              data: {}
            });
          }
        }, {enableHighAccuracy: true})
      }
      ,
      address(callback) {
        !this.search && (this.search = '小区,商铺,楼盘,店铺,商店,超市');
        this.map.centerAndZoom(new this.BMap.Point(info.lng, info.lat), 11);
        const local = new this.BMap.LocalSearch(this.map, {
          onSearchComplete: (results) => {
            if (local.getStatus() === 0) {
              this.flag = false;
              callback && callback({
                code: 200,
                data: {address: results.Ar || []}
              })
            } else {
              this.flag = false;
              callback && callback({
                code: 404,
                data: {address: []}
              })
            }
          }
        });
        local.search(this.search);
      }
      ,
    }
  }
</script>

<style scoped lang="less">
    .baiduMap {
        position: fixed;
        width: 100%;
        height: 100%;
        overflow-y: auto;
        top: 0;
        left: 0;
        z-index: -1;
        width: 0;
        height: 0;
        display: none;

        > div {
            width: 100%;
            height: 100%;
        }
    }
</style>
