<template>
    <div class="content_top searchIndex">
        <div class="build_search">
            <div class="left" @click="()=>{this.$navigation.back(this)}">
                <img src="../../../assets/img/icon/ic_arrow_left_return_black.png" alt="">
            </div>
            <div class="center">
                <div class="icon">
                    <img src="../../../assets/img/home/ic_search_gray.png" alt="">
                </div>
                <div class="input">
                    <input type="text" placeholder="搜索店铺名称/服务类型" v-model="params.keywords"/>
                </div>
            </div>
            <div class="right" @click="submit">
                搜索
            </div>
        </div>
        <div v-show="!list.length" class="default">
            <img src="../../../assets/img/home/pic_default_search.png" alt="">
            <p v-if="flag===1">暂无搜索结果</p>
            <p v-if="flag===0"></p>
        </div>
        <div v-show="list.length" class="body">
            <item v-for="(item,index) in list " :key="index" :data="item" type="2"/>
            <load-more ref="loadMore" url="shopList"  :autoload="false" :params="params" @success="success"></load-more>
        </div>
    </div>
</template>

<script>
  import Item from "../../../components/shop/item";
  import LoadMore from "../../../components/public/loadMore";

  export default {
    name: "haoyinSearchIndex",
    components: {LoadMore, Item},
    data() {
      return {
        flag: 0,
        list: [],
        params: {
          keywords: '',
          shop_id:this.$route.query.shop_id||'',
          category_id:this.$route.query.category_id||'',//平台分类id
          shop_category_id:this.$route.query.shop_category_id||'',//店铺分类id
        },
      }
    },
    mounted(){
      if (this.$route.query.category_id){
        this.submit();
      }
    },
    methods: {
      success(res) {
        this.list = this.list.concat(res)
      },
      submit() {
        this.list = [];
        this.flag=1;
        this.$refs.loadMore.restart();
      }
    }
  }
</script>

