<template>
    <div class="content_top walletIndex">
        <nav-bar title="我的钱包"></nav-bar>
        <div class="body">
            <div class="top">
                <div>
                    <h1>余额</h1>
                    <p>¥&nbsp;{{account}}</p>
                </div>
            </div>
            <div class="title">交易明细</div>
            <div v-show="!list.length" class="noData">
                <img src="../../../assets/img/user/pic_default_transaction_details.png" alt="">
                <p>暂无交易明细</p>
            </div>
            <div v-show="list.length" class="list">
                <div class="item" v-for="(item,index) in list" :key="index">
                    <div class="title">
                        {{item.info}}
                    </div>
                    <div class="body">
                        <div class="time">{{item.created_at}}</div>
                        <div class="money">{{item.money}}元</div>
                    </div>
                </div>
                <load-more
                    url="accountLog"
                    @success="this.success"
                />
            </div>
        </div>
    </div>
</template>
<script>
  import LoadMore from "../../../components/public/loadMore";
  import {mapState,mapActions} from 'vuex';
  export default {
    name: "walletIndex",
    components: {LoadMore},
    computed:{
      ...mapState({
        account:state=>state.user.balance
      })
    },
    data() {
      return {
        list:[]
      }
    },
    beforeMount() {
      this.getBalance();
    },
    methods: {
      ...mapActions([
        'getBalance'
      ]),
      success(res){
        this.list=this.list.concat(res);
      },
    }
  }
</script>

