<template>
    <div class="content_top searchIndex">
        <div class="build_search">
            <div class="left" @click="()=>{this.$navigation.back(this)}">
                <img src="../../../assets/img/icon/ic_arrow_left_return_black.png" alt="">
            </div>
            <div class="center">
                <div class="icon">
                    <img src="../../../assets/img/home/ic_search_gray.png" alt="">
                </div>
                <div class="input">
                    <input type="text" placeholder="搜索店铺名称/服务类型" v-model="params.keyword"/>
                </div>
            </div>
            <div class="right" @click="submit">
                搜索
            </div>
        </div>
        <div v-show="!list.length" class="default">
            <img src="../../../assets/img/home/pic_default_search.png" alt="">
            <p>暂无搜索结果</p>
        </div>
        <div v-show="list.length" class="body">
            <item v-for="(item,index) in list " :key="index" :data="item"/>
            <load-more ref="loadMore" url="serviceShop" :autoload="false" :params="params" @success="success"></load-more>
        </div>
    </div>
</template>

<script>
  import Item from "../../../components/shop/item";
  import LoadMore from "../../../components/public/loadMore";

  import {mapState, mapActions} from 'vuex';

  export default {
    name: "searchIndex",
    components: {LoadMore, Item},
    computed: {
      ...mapState({
        map: state => state.config.map,
      })
    },
    data() {
      return {
        flag: 0,
        list: [],
        params: {
          keyword: '',
          lng:'',
          lat:''
        },
      }
    },
    mounted() {
      if (this.map.lng && this.map.lat) {
        this.params.lng = this.map.lng;
        this.params.lat = this.map.lat;
        this.$refs.loadMore.restart();
      } else {
        this.$showLoading('正在获取定位');
        this.$locationPoint(res => {
          this.$hideLoading();
          if (res) {
            if (res.code === 200) {
              this.params.lng = res.data.lng;
              this.params.lat = res.data.lat;
              this.setMap(res.data);
              this.$refs.loadMore.restart();
            } else {
              this.$alert('获取定位失败,请稍后再试');
            }
          } else {
            this.$alert('获取定位失败,请稍后再试');
          }
        });
      }
    },
    methods: {
      ...mapActions([
        'setMap'
      ]),
      success(res) {
        this.list = this.list.concat(res)
      },
      submit() {
        this.list = [];
        this.$refs.loadMore.restart();
      }
    }
  }
</script>

