<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "userIndex",
    data() {
      return {
        count: {
          service: {
            comment: 0,
            confirm: 0,
            no_pay: 0,
            no_receive: 0,
          },
          shop: {
            comment: 0,
            no_pay: 0,
            no_receive: 0,
            no_send: 0,
          }
        }
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo,
        token:state => state.user.token,
      })
    },
    mounted() {
      try {
        if (this.token.access_token) {
          this.getUserInfo();
          this.countOrder();
        }
      } catch (e) {
        throw Error(e);
      }
    },
    methods: {
      ...mapActions([
        'getUserInfo'
      ]),
      countOrder() {
        this.$http('countOrder').then(res => {
          this.count = {
            ...res
          }
        })
      }
    }
  }
</script>

